


















import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";

import AbstractOfferSection from "./../AbstractOfferSection.vue";
import Login from "./Login.vue";
import Register from "./Register.vue";
import GaHelper from "../../service/GaHelper";
import StorageSync from "../../../modules/chrome/storage/StorageSync";
import {myPicodiUrl, storageSyncKey, accountRedirectType, gaParameters} from "../../../modules/constants";
import { getBrowserParameter } from "../../../modules/functions";

@Component({
  components: {
    'Login': Login,
    'Register': Register
  }
})

export default class Account extends AbstractOfferSection {
  @Prop(String) activeView!: string;
  @Prop(String) defaultView!: string;

  myPicodiUrl: string = myPicodiUrl;
  locale: string = this.$store.getters[('locale/getLocale')];
  installationId!: string;

  redirectTab(options: {[index: string]: accountRedirectType}): void{
    const { type } = options;

    const url: string = this.getRedirectUrl(type || accountRedirectType.LOGIN_MP);
    this.createTab(url, true);
  }

  getRedirectUrl(type: accountRedirectType): string{
    this.sendGaEvent(type);
    switch(type) {
      case accountRedirectType.LOGIN_MP:
        return this.getLoginMyPicodiUrl();
      case accountRedirectType.REGISTER_MP:
        return this.getRegistrationMyPicodiUrl();
      case accountRedirectType.LOGIN_FB:
        return this.getLoginFacebookUrl();
      case accountRedirectType.REGISTER_FB:
        return this.getRegistrationFacebookUrl();
      case accountRedirectType.LOGIN_GOOGLE:
        return this.getLoginGoogleUrl();
      case accountRedirectType.REGISTER_GOOGLE:
        return this.getRegistrationGoogleUrl();
      case accountRedirectType.LOGIN_APPLE:
        return this.getLoginAppleUrl();
      default:
        return this.getLoginMyPicodiUrl();
    }
  }

  getLoginMyPicodiUrl(): string{
    return `${this.getMyPicodiUrl()}/${this.getLocale()}/sign-in?register_placement=/addon/login_screen${this.getParams()}`
  }

  getRegistrationMyPicodiUrl(): string{
    return `${this.getMyPicodiUrl()}/${this.getLocale()}/sign-up?register_placement=/addon/register_screen${this.getParams()}`
  }

  getLoginFacebookUrl(): string{
    return `${this.getMyPicodiUrl()}/${this.getLocale()}/connect/facebook?register_placement=/addon/login_screen/facebook${this.getParams()}`
  }

  getRegistrationFacebookUrl(): string{
    return `${this.getMyPicodiUrl()}/${this.getLocale()}/connect/facebook?register_placement=/addon/register_screen/facebook${this.getParams()}`
  }

  getLoginGoogleUrl(): string{
    return `${this.getMyPicodiUrl()}/${this.getLocale()}/connect/google?register_placement=/addon/login_screen/google${this.getParams()}`
  }

  getRegistrationGoogleUrl(): string{
    return `${this.getMyPicodiUrl()}/${this.getLocale()}/connect/google?register_placement=/addon/register_screen/google${this.getParams()}`
  }

  getLoginAppleUrl(): string{
    return `${this.getMyPicodiUrl()}/${this.getLocale()}/connect/apple?register_placement=/www/login_screen/apple${this.getParams()}`
  }

  getMyPicodiUrl(): string{
    return this.myPicodiUrl;
  }

  getLocale(): string{
    return this.locale;
  }

  getParams(): string{
    return `&addon_iid=${this.installationId}&referer=${window.location.href}&browser=${getBrowserParameter().toLowerCase()}`;
  }

  isRegisterDefaultView(): boolean{
    return this.apiConfig('AddOn.Chrome.DefaultAccountViewIsRegister') ?? false;
  }

  continueAsGuest(): void {
    GaHelper.pushEvent(`guest`, {
      [gaParameters.PAGE_TITLE]: `login_prompt`,
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/login`,
    });
    this.setDefaultView();
  }

  setDefaultView(): void{
    this.$store.commit('views/setDefault', this.$store.getters[('views/getDefault')]);
    this.$store.commit('views/setActive', this.$store.getters[('views/getDefault')]);
  }

  async getInstallation(): Promise<{[index: string]:string}> {
    return await StorageSync.get(storageSyncKey.INSTALLATION);
  }

  async setInstalationId(): Promise<any> {
    const { installationId } = await this.getInstallation();
    this.installationId = installationId;
  }

  sendGaEvent(type: accountRedirectType): void{
    const actionObject: {[key in accountRedirectType]: string} = {
      [accountRedirectType.LOGIN_MP]: 'email',
      [accountRedirectType.LOGIN_FB]: 'facebook',
      [accountRedirectType.LOGIN_GOOGLE]: 'google',
      [accountRedirectType.LOGIN_APPLE]: 'apple',
      [accountRedirectType.REGISTER_MP]: 'register',
      [accountRedirectType.REGISTER_FB]: 'register',
      [accountRedirectType.REGISTER_GOOGLE]: 'register'
    }

    if(actionObject.hasOwnProperty(type)){
      GaHelper.pushEvent(`${actionObject[type] === 'register' ? 'register' : 'login'}`, {
        [gaParameters.PAGE_TITLE]: `login_prompt`,
        [gaParameters.PAGE_LOCATION]: `/${this.locale}/login`,
        [gaParameters.LOGIN_TYPE]: actionObject[type]
      });
    }
  }

  mounted(){
    this.setInstalationId();
  }
}
