






























import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";

import CaaModalProgress from "./CaaModalProgress.vue";
import CaaModalFailed from "./CaaModalFailed.vue";
import CaaModalSuccess from "./CaaModalSuccess.vue";
import CouponApplier from "./../../../caa/CouponApplier";
import GaHelper from "../../../service/GaHelper";
import Shop from "../../../sections/structures/Shop";
import AbstractOfferSection from "../../AbstractOfferSection.vue";
import {gaParameters} from "../../../../modules/constants";
import ErrorTracker from "../../../../modules/ErrorTracker";

@Component({
  components: {
    CaaModalSuccess,
    CaaModalFailed,
    CaaModalProgress
  }
})

export default class CaaModal extends AbstractOfferSection {
  @Prop(String) modalType!: string;
  @Prop(Object) bestCoupon!: Object;
  @Prop(Number) totalPriceInPage!: number;
  @Prop(CouponApplier) couponApplier!: CouponApplier;
  @Prop(String) currentCoupon!: string;
  @Prop(String) shopUrlName!: string;
  @Prop(Boolean) isSubsidExist!: boolean;
  @Prop(Boolean) isFpoEnabled!: boolean;

  shop: Shop = this.$store.getters[('shop/getShop')];
  locale: string = this.$store.getters[('locale/getLocale')];

  closeCaaModal(): any{
    if(this.modalType === 'PROGRESS'){
      this.couponApplier.caaAnalyticsHelper.sendAnalytics('CANCEL');
    }
    this.resetCaa();
  }

  resetCaa(): any{
    this.couponApplier.caaReset()
      .then(() => {
        this.$emit('set-modal-type', '');
      })
      .catch(() => {
        ErrorTracker.trackError('Reset caa error.')
      })
  }

  sendGaEvent(action: string): any {
    GaHelper.pushEvent(`${action}_ok`, {
      [gaParameters.PAGE_TITLE]: `caa_${action}`,
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/caa/${this.shopUrlName}/${action}`,
      [gaParameters.SHOP_URL_NAME]: this.shopUrlName
    });
  }

  continueButtonClick(action: string) {
    this.$emit('set-modal-type', '');
    this.resetCaa();
    this.sendGaEvent(action);

    if(!this.isSubsidExist && this.shop.isCashback()) {
      const picodiActivateCashbackModalUrl = this.getPicodiActivateCashbackModalUrl(this.locale, this.shop.shopUrlName, this.shop.shopId, this.shop.versionRedirect);
      this.createTab(picodiActivateCashbackModalUrl, true);
    }
  }

  getShopDescription(type: string): string {
    if(!!this.shop.fpoCashbackDesc && this.isFpoEnabled) {
      return `<b class="modal-${type}__title--small">${this.shop.cashbackDesc}</b><b>${this.shop.fpoCashbackDesc}</b>`;
    }else if(!!this.shop.premiumCashbackDesc){
      return `<b class="modal-${type}__title--small">${this.shop.cashbackDesc}</b><b>${this.shop.premiumCashbackDesc}</b>`;
    }else {
      return `<b>${this.shop.cashbackDesc}</b>`;
    }
  }
}
