export default {
  api: {
    'api.get.translations': function() {
      return `/translations?v=${Date.now()}`;
    },
    'api.get.domains': function() {
      return `/domains`;
    },
    'api.get.caa': function() {
      return `/configs.json?v=${Date.now()}`;
    },
    'api.get.shop': function(params) {
      return `/shop/${params.shopUrlName}?v=${Date.now()}`;
    },
    'api.get.custom.prompt.list': function() {
      return `/custom/prompt?v=${Date.now()}`;
    },
    'api.get.custom.prompt.details': function(params) {
      return `/custom/prompt/${params.customPromptId}?v=${Date.now()}`;
    },
    'api.get.serp.decorations': function() {
      return `/serp?v=${Date.now()}`;
    },
    'api.addon.config': function() {
      return `/config-data`;
    },
    'api.set.config': function(params) {
      return `/save-config?installationId=${params.installationId}&configuration=${params.configuration}`;
    },
    'api.addon.install': function(params) {
      return `/install?installationId=${params.installationId}&token=${params.token}&defaultLang=${params.defaultLang}&browserLang=${params.browserLang}&userAgent=${params.userAgent}&platform=${params.platform}&installationDate=${params.installationDate}${params.subsid ? '&userId=' + params.subsid : ''}`;
    },
    'api.search': function() {
      return '/search';
    },
    'api.shop.suggestions': function(params) {
      return `/suggestions/${params.shopUrlName}?v=${Date.now()}`;
    },
    'api.get.user.properties': function(params) {
      return `/cashback/prompt/${params.installationId}`;
    },
    'my-picodi.get.user.data': function() {
      return `/api/user-data`;
    }
  }
};