import GaHelper from "../content/service/GaHelper";

class ErrorTracker {
  trackError(message: string | Error): void {
    const error = !(message instanceof Error) ? new Error(message) :  message;
    this.sendErrorToGA4({
      'message': error.message,
      'stack': error.stack,
    });
    this.logErrorToConsole(error);
  }

  private sendErrorToGA4(error: object): void {
    GaHelper.pushError(error);
  }

  private logErrorToConsole(error: Error): void {
    console.error(error);
  }
}

export default new ErrorTracker();