import browser from "../../../chrome/nameSpace";
import isUndefined from "lodash/isUndefined";
import MessageRequestInterface from "../interfaces/MessageRequestInterface";
import Tab = browser.tabs.Tab;
import MessageSender = browser.runtime.MessageSender;
import isEmpty from "lodash/isEmpty";
import ErrorTracker from "../../../ErrorTracker";

class MessageRequestResponse {
  protected type: string;
  protected async: boolean = false;
  protected tab: Tab = null;
  protected values: any = {};
  protected tabSender: Tab = null;
  protected responseSender: Function;

  constructor (request: MessageRequestInterface, sender: MessageSender, responseSender: Function) {
		this.type = request.type;
		this.tab = request.tab;
		this.values = request.values;
		this.async = request.async;
    this.tabSender = sender.tab;
		this.responseSender = responseSender;
  }

  public getType(): string {
    return this.type;
  }

  public isAsync(): boolean {
    return this.async;
  }

  public getTab(): Tab {
    return this.tab;
  }

  public getTabSender(): Tab {
    return this.tabSender;
  }

  public getValue(key: string, defaultValue: any = undefined): any {
    if (!this.values.hasOwnProperty(key)) {
    	if (isUndefined(defaultValue)) {
        ErrorTracker.trackError(`Unable to find key "${key}" and default value is not defined!`);
        //throw new Error(`Unable to find key "${key}" and default value is not defined!`);
			}
      return defaultValue;
    }
    return this.values[key];
  }

  public getValues(): any {
    return this.values;
  }

  public sendResponse(data: any): void {
    this.responseSender(!isEmpty(data) ? JSON.parse(JSON.stringify(data)) : data);
  }
}

export default MessageRequestResponse