import Message from "../../modules/chrome/message/MessageContent";
import MessageRequest from "../../modules/chrome/message/structures/MessageRequest";
import HttpResponse from "../../modules/api/structures/HttpResponseContent";
import extensionVersion from "../../modules/chrome/version";
import {storageSyncKey} from "../../modules/constants";
import browser from "../../modules/chrome/nameSpace";
import isEmpty from "lodash/isEmpty";
import ErrorTracker from "../../modules/ErrorTracker";

export default class ApiDataManager {
  apiTypeKey: string;
  apiValues: object;
  ttlHours: number;
  ttlHoursEmptyResponse: number;

  constructor(apiTypeKey: string, apiValues: object, ttlHours: number = 24, ttlHoursEmptyResponse: number = ttlHours) {
    this.apiTypeKey = apiTypeKey;
    this.apiValues = apiValues;
    this.ttlHours = ttlHours;
    this.ttlHoursEmptyResponse = ttlHoursEmptyResponse;
  }

  getData(checkData: Array<string | number> = null) {
    try {
      return this.getStorageData(checkData)
        .then(storageData => {
          return storageData;
        })
        .catch(() => {
          return this.getApiData()
            .then(apiData => {
              this.setStorageData(apiData);
              return apiData;
            })
            .catch((error) => {
              ErrorTracker.trackError(error);
              //throw new Error(error)
            })
        })
    }catch (error) {
      ErrorTracker.trackError(error);
      //throw new Error(error);
    }

  }

  getApiData(): Promise<object> {
    return new Promise((resolve, reject) => {
      Message.send((messageRequest: MessageRequest) => {
        messageRequest
          .setType(this.apiTypeKey)
          .setAsync(true);

        for(const [key, value] of Object.entries(this.apiValues)) {
          messageRequest.setValue(key, value);
        }

        messageRequest
          .setHttpResponseCallback((httpResponse: HttpResponse) => {
            const response = httpResponse.getResponse();
            if (httpResponse.isSuccessful()) {
              resolve(response.data.data ? response.data.data : {[storageSyncKey.CAA]: response.data}); //TODO
            } else {
              reject(`Problem with downloading data from the API - ${this.apiTypeKey}`);
            }
          });
      });
    });
  }

  setStorageData(data: object): void {
  const dataToSet = isEmpty(data) ?
    Object.assign(
    {},
    {expireDate: this.getExpiredDate(this.ttlHoursEmptyResponse) },
    {extensionVersion: this.getExtensionVersion()},
    this.apiValues
  ) :
    Object.assign(
      data,
      {expireDate: this.getExpiredDate(this.ttlHours)},
      {extensionVersion: this.getExtensionVersion()},
      this.apiValues
    );
    browser.storage.local.set(JSON.parse(JSON.stringify({[this.apiTypeKey]: dataToSet})));
  }

  getStorageData(checkData: Array<string | number> = null): Promise<object|false>{
    return browser.storage.local.get(this.apiTypeKey)
      .then((storage: any) => {
        const storageData = storage[this.apiTypeKey];
        if (storageData &&
          !this.isUpdateExtensionVersion(storageData) &&
          !this.isStorageExpired(storageData) && (!checkData || this.isDataExist(storageData, checkData))
        ) {
          return storageData;
        } else {
          ErrorTracker.trackError(`Problem with downloading data from the local storage`);
          //throw new Error('Problem with downloading data from the local storage');
        }
      })
      .catch(() => {
        ErrorTracker.trackError(browser.runtime.lastError ? browser.runtime.lastError.message : 'Problem with downloading data from the local storage');
        //throw new Error(browser.runtime.lastError ? browser.runtime.lastError.message : 'Problem with downloading data from the local storage');
      })
  }

  removeStorageData(): void{
    browser.storage.local.clear();
  }

  getExpiredDate(hours :number): number {
    const expireDate = new Date();
    expireDate.setTime(expireDate.getTime() + 60 * 60 * hours * 1000);
    return expireDate.getTime();
  }

  getExtensionVersion(): string {
    return extensionVersion.get();
  }

  isUpdateExtensionVersion(data: { extensionVersion: string }): boolean {
    return !data.extensionVersion || data.extensionVersion !== this.getExtensionVersion();
  }

  isStorageExpired(data: { expireDate: number }): boolean {
    const now = new Date().getTime();
    return !data.expireDate || data.expireDate < now;
  }

  isDataExist(data: { [key: string]: any } , checkData: Array<string | number>): boolean {
    return data[checkData[0]] && data[checkData[0]] === checkData[1];
  }
}