import ErrorTracker from "./ErrorTracker";

export default {
  generateId (len: number): string {
   try {
     const arr = new Uint8Array(len);
     self.crypto.getRandomValues(arr);
     return Array.from(arr, function(dec) { return Math.floor(dec / 16).toString(16); }).join('');
   }catch (e){
     ErrorTracker.trackError(`Unable to generate installation id - ${e}`)
   }
  }
}