






























import Component from "vue-class-component";
import {Prop} from "vue-property-decorator";

import Offers from "./Offers.vue";
import Cashback from "./Cashback.vue";
import AbstractOfferSection from "./../AbstractOfferSection.vue";
import StorageSync from "../../../modules/chrome/storage/StorageSync";
import ShopStructure from "../../sections/structures/Shop";
import {storageSyncKey} from "../../../modules/constants";

@Component({
  components: {
    'Offers' : Offers,
    'Cashback': Cashback
  }
})

export default class Shop extends AbstractOfferSection {
  @Prop(String) searchShopName!: string;
  @Prop(Boolean) isSubsidExist!: boolean;
  @Prop(Boolean) isRecognizeShop!: boolean;
  @Prop(Boolean) isFpoEnabled!: boolean;

  shop: ShopStructure = this.$store.getters[('shop/getShop')];
  currentTab: string = 'Cashback';
  tabs: Array<string> = ['Cashback', 'Offers'];
  tabsTitle: Object = {'Cashback': this.trans('addon.shop.cashback'), 'Offers': this.trans('addon.shop.codes.promos')};

  onClickTab(tabName: string) {
    this.setCurrentTab(tabName);
    this.setInStorageOffersViewIsActive(tabName);
  }

  isShopCashbackDisabled() {
    return !this.shop.isCashback();
  }

  isSubsidNotExist() {
    return !this.isSubsidExist;
  }

  setCurrentTab(tabName: string) {
    this.currentTab = tabName;
  }

  setOfferCurrentTabFromStorage() {
    this.getFromStorageOffersViewIsActive()
      .then((parsedOffersViewIsActive) => {
        if(parsedOffersViewIsActive[this.shop.shopId]){
          this.currentTab = 'Offers';
        }
      })
  }

  async getFromStorageOffersViewIsActive() {
    const offersViewIsActive = await StorageSync.get(storageSyncKey.OFFER_VIEW_IS_ACTIVE);
    return offersViewIsActive ? JSON.parse(offersViewIsActive) : {};
  }

  async setInStorageOffersViewIsActive(tabName: string) {
    const parsedOffersViewIsActive = await this.getFromStorageOffersViewIsActive();

    if(tabName === 'Offers') {
      parsedOffersViewIsActive[this.shop.shopId] = true;
    }else{
      delete parsedOffersViewIsActive[this.shop.shopId];
    }

    StorageSync.set({[storageSyncKey.OFFER_VIEW_IS_ACTIVE]: JSON.stringify(parsedOffersViewIsActive)});
  }

  mounted () {
    if(this.isShopCashbackDisabled() || this.isSubsidNotExist()) {
      this.setCurrentTab('Offers')
    }

    this.setOfferCurrentTabFromStorage();
  }
}
