import StorageSync from "../modules/chrome/storage/StorageSync";
import {storageSyncKey} from "../modules/constants";
import ErrorTracker from "../modules/ErrorTracker";

export default class Config {
  private addonConfig: any;

  constructor() {
    this.getStorageConfig();
  }

  public getStorageConfig(): Promise<any> {
    return StorageSync.get(storageSyncKey.ADDON_CONFIG)
      .then(addonConfig => {
        if (typeof addonConfig !== 'undefined') {
          return this.addonConfig = addonConfig;
        }else{
          return this.addonConfig = {};
        }
      })
      .catch((e) => {
        ErrorTracker.trackError(`Unable to get ${storageSyncKey.ADDON_CONFIG} from storage sync - ${e}`)
      })
  }

  public getConfig(): object {
    return this.addonConfig;
  }

  private setConfig(): any {
    StorageSync.set({[storageSyncKey.ADDON_CONFIG]: this.addonConfig});
  }

  public removeKeys(key: string): any {
    for (const item in this.addonConfig) {
      if (item.split('-')[0] === key) {
        delete this.addonConfig[item];
      }
    }
    this.setConfig();
  }

  public setKey(configName: string, configValue: number): any {
    if(configValue == 0){
      this.addonConfig[configName] = configValue;
    }else{
      delete this.addonConfig[configName];
    }
    this.setConfig();
  }

}