











import Vue from "vue";
import Component from "vue-class-component";
import {mapGetters} from "vuex";

import Shop from "../../sections/structures/Shop";
import GaHelper from "../../service/GaHelper";
import cashbackAvailableView from "../../service/CashbackAvailableView";
import Message from "../../../modules/chrome/message/MessageContent";
import MessageRequest from "../../../modules/chrome/message/structures/MessageRequest";
import {gaParameters, messageName} from "../../../modules/constants";
import PopupVisibility from "../../service/PopupVisibility";

const ONE_DAY_IN_MS = 86400000;
const NO_CASHBACK_PROMPT_HIDE_TIME = 30*ONE_DAY_IN_MS;

@Component({
  computed: {
    ...mapGetters({
      getActiveView: 'views/getActive'
    })
  },
})
export default class DropDownMenu extends Vue {
  shop: Shop = this.$store.getters[('shop/getShop')];
  locale: string = this.$store.getters[('locale/getLocale')];
  getActiveView!: string;

  hidePromptToday(): void {
    cashbackAvailableView.hideToday(this.shop.shopId);
    this.gaPushEvent('hide_24');
    this.setActiveOffersView();
    PopupVisibility.toggle();
  }

  hidePromptInStore(): void {
    if(this.isNoCashbackPrompt()){
      this.hideNoCashbackPromptInStore();
    }else{
      this.hideCashbackPromptInStore();
    }
  }

  hideCashbackPromptInStore(): void {
    this.gaPushEvent('hide_for_shop');
    this.disabledShopInExtensionSettings();
    window.location.reload();
  }

  hideNoCashbackPromptInStore(): void {
    this.gaPushEvent('hide_for_shop');
    cashbackAvailableView.hideInTimeFromConfig(this.apiConfig('AddOn.Chrome.configHideNoCashbackPromptInMs') || NO_CASHBACK_PROMPT_HIDE_TIME, this.shop.shopId);
    window.location.reload();
  }

  disabledShopInExtensionSettings(): void{
    this.setStorageAddonConfig(`stores_all`, `0`);
    this.setStorageAddonConfig(`shop-${this.shop.shopId}`, `0`);
  }

  setStorageAddonConfig(name: string, value: string): void {
    Message.send((messageRequest: MessageRequest) => {
      messageRequest
          .setType(messageName.SET_CONFIG_KEY)
          .setValue('configName', name)
          .setValue('configValue', value);
    });
  }

  setActiveOffersView(): void {
    if(!this.isNoCashbackPrompt()) {
      this.$store.commit('views/setActive', 'OFFERS');
      this.$store.commit('views/setDefault', 'OFFERS');
    }
  }

  gaPushEvent(name: string): void {
    GaHelper.pushEvent(name, {
      [gaParameters.PAGE_TITLE]: this.isNoCashbackPrompt() ? 'no_cashback_prompt': 'cashback_prompt',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/${this.isNoCashbackPrompt() ? 'no-cb' : 'cb'}/${this.shop.shopUrlName}/notify`,
      [gaParameters.SHOP_URL_NAME]: this.shop.shopUrlName
    });
  }

  isNoCashbackPrompt(): boolean {
    return this.getActiveView === 'NOCASHBACKPROMPT';
  }
}
