





















import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
import { mapGetters } from "vuex";

import ApiConfig from "../../service/ApiConfig";
import StorageSync from "../../../modules/chrome/storage/StorageSync";
import {storageSyncKey} from "../../../modules/constants";
import countriesCodeMap from "../../../static/data/countiesCodeMap/countriesCodeMap";

@Component({
  computed: {
    ...mapGetters({
      getDefaultView: 'views/getDefault',
    })
  },
})
export default class Select extends Vue {
  @Prop(String) defaultView!: string;
  @Prop(Boolean) showSaveButton: boolean;

  locale: string = this.$store.getters[('locale/getLocale')];
  countries: { [key: string]: string; } = countriesCodeMap;
  isListOpen: boolean = false;
  activeCountryCode: string = this.locale;
  activeCountryLabel: string;
  getDefaultView!: () => string;

  private getActiveCountryLabel(): string{
    return this.countries[this.activeCountryCode];
  }

  private onSaveLocale(): void{
    StorageSync.set({[storageSyncKey.LOCALE]: this.activeCountryCode});
    StorageSync.set({[storageSyncKey.IS_INSTALLED]: true});

    this.$store.commit('locale/setLocale', this.activeCountryCode);
    if(this.locale !== this.activeCountryCode){
      ApiConfig.remove();
      this.$emit('set-api-addon-config', this.activeCountryCode);
    }else{
      this.$store.commit('views/setActive', this.getDefaultView);
    }
    window.location.reload();
  }

  private onChangeLocale(event: Event): void{
    const element = event.target as HTMLInputElement
    this.isListOpen = !this.isListOpen;
    this.activeCountryCode = element.id || element.parentElement.id;
    this.activeCountryLabel = this.getActiveCountryLabel();

    if(!this.showSaveButton){
      StorageSync.set({[storageSyncKey.LOCALE]: this.activeCountryCode});
      this.$store.commit('locale/setLocale', this.activeCountryCode);
      ApiConfig.remove();
      this.$emit('set-api-addon-config', this.activeCountryCode);
      window.location.reload();
    }
  }

  created(): void{
    this.activeCountryLabel = this.getActiveCountryLabel();
  }
}
