



































import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
import GaHelper from "../../service/GaHelper";
import Checkbox from "./../element/Checkbox.vue";
import {gaParameters} from "../../../modules/constants";

@Component({
  components: {Checkbox}
})

export default class Stores extends Vue {
  @Prop(Array) shopList!: Array;
  @Prop({default: true}) disabled!: boolean;
  @Prop(Boolean) messageText!: boolean;
  @Prop(Object) config!: Object;

  locale: string = this.$store.getters[('locale/getLocale')];

  setDefaultDisabledAll() {
    this.disabled = !('stores_all' in this.config);
  }

  toggleDisabledAll() {
    this.disabled = !this.disabled;
  }

  mounted() {
    GaHelper.pushPageView({
      [gaParameters.PAGE_TITLE]: 'settings_stores',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/settings/stores`
    });
  }
}
