import ApiDataManager from "./ApiDataManager";
import browser from "../../modules/chrome/nameSpace";

class ApiConfig {
  get(locale: string) {
    const apiConfig = new ApiDataManager('api.config', {locale: locale});

    return apiConfig.getData()
      .then((data) => {
        return data;
      });
  }

  remove() {
    browser.storage.local.clear();
  }
}

export default new ApiConfig();