



































import Component from "vue-class-component";
import AbstractOfferSection from "./../AbstractOfferSection.vue";
import PopupVisibility from "../../service/PopupVisibility";
import Offer from "../../sections/structures/Offer";
import GaHelper from "../../service/GaHelper";
import Shop from "../../sections/structures/Shop";
import NoticePromptAnimation from "../../animations/notice-prompt";
import LottieAnimation from "lottie-web-vue";
import StorageSync from "../../../modules/chrome/storage/StorageSync";
import {gaParameters, storageSyncKey} from "../../../modules/constants";
import {Prop} from "vue-property-decorator";

@Component({
  components: {
    'LottieAnimation': LottieAnimation
  }
})
export default class NoticePrompt extends AbstractOfferSection {
  @Prop(Boolean) isFpoEnabled!: boolean;
  shop: Shop = this.$store.getters[('shop/getShop')];
  offers: Array<Offer> = this.$store.getters[('offers/getOffers')];
  locale: string = this.$store.getters[('locale/getLocale')];
  defaultView: string = this.$store.getters[('views/getDefault')];
  animation = NoticePromptAnimation;

  complete(){
    this.closePopup();
    this.setTimeoutActiveOffersView();
  }

  setTimeoutActiveOffersView(){
    setTimeout(this.setOffersView, 500);
  }

  setOffersView() {
    this.$store.commit('views/setActive', 'OFFERS');
    this.$store.commit('views/setDefault', 'OFFERS');
  }

  closePopup() {
    PopupVisibility.close();
  }

  saveTheShowNoticePrompt() {
    StorageSync.set({[storageSyncKey.IS_NOTICE_PROMPT_WAS_ONCE_VISIBLE]: {[this.shop.shopId]: true}});
  }

  mounted() {
    this.saveTheShowNoticePrompt();
    GaHelper.pushPageView({
      [gaParameters.PAGE_TITLE]: 'notice_prompt',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/cb/${this.shop.shopUrlName}/notice`,
      [gaParameters.SHOP_URL_NAME]: this.shop.shopUrlName
    });
  }

  destroyed() {
    this.setOffersView();
  }
}
