












import AbstractOfferSection from "./../AbstractOfferSection.vue";
import {CustomPromptDetails} from "../../sections/structures/CustomPromptDetails";
import Component from "vue-class-component";
import {Prop} from "vue-property-decorator";
import isEmpty from "lodash/isEmpty";
import CustomPrompt from "../../service/CustomPrompt";
import cashbackActivations from "../../../modules/cashbackActivations";
import GaHelper from "../../service/GaHelper";
import {gaParameters, redirectType} from "../../../modules/constants";

@Component({})
export default class CustomPromptImage extends AbstractOfferSection {
  @Prop(Boolean) isSubsidExist!: boolean;
  @Prop(CustomPrompt) customPrompt!: CustomPrompt;
  locale: string = this.$store.getters[('locale/getLocale')];
  customPromptDetails: CustomPromptDetails = this.$store.getters[('customPrompt/getCustomPrompt')];

  async getMetricRedirectUrl(){
    return this.generateOfferMetricUrl({
      locale: this.locale,
      customPromptId: this.customPrompt.getCustomPromptId(),
      versionRedirect: this.customPromptDetails.redirect_version,
      redirectType: redirectType.CUSTOM_PROMPT_IMAGE,
      shopNumber: 1
    });
  }

  getPicodiModalUrl(){
    return this.getPicodiActivateCashbackModalUrl(
        this.locale,
        this.customPromptDetails.shopUrlName,
        this.customPromptDetails.shopId,
        this.customPromptDetails.shopVersionRedirect
    );
  }

  sendGaEvent(shopUrlName: string): void{
    GaHelper.pushEvent(`redirect`, {
      [gaParameters.PAGE_TITLE]: `custom_prompt`,
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/custom-prompt`,
      [gaParameters.SHOP_URL_NAME]: shopUrlName
    });
  }

  async onClickCustomPrompt(){
    if(!isEmpty(this.customPromptDetails.direct_url)) {
      this.createTab(this.customPromptDetails.direct_url, true);
      this.sendGaEvent(this.customPromptDetails.direct_url);
    }else{
      cashbackActivations.save(`${this.customPromptDetails.shopId}s`);
      this.createTab(
          this.isSubsidExist ? await this.getMetricRedirectUrl() : this.getPicodiModalUrl(),
          true);
      this.sendGaEvent(this.customPromptDetails.shopUrlName);
    }
  }
}
