







































import Vue from "vue";
import GaHelper from "../../content/service/GaHelper";
import Component from "vue-class-component";
import {Prop} from "vue-property-decorator";
import {gaParameters} from "../../modules/constants";

@Component
export default class PromoIcon extends Vue {
  @Prop(Number) numberOfOffers!: number;
  @Prop(Boolean) isPopupActive!: boolean;
  @Prop(String) shopUrlName!: string;

  locale: string = this.$store.getters[('locale/getLocale')];

  onClick() {
    this.$emit('toggle-popup');
    this.sendGaTogglePopup();
  }

  sendGaTogglePopup() {
    const shopUrlName = this.shopUrlName ? this.shopUrlName : 'n/a';
    if(!this.isPopupActive) {
      GaHelper.pushEvent('open', {
        [gaParameters.SHOP_URL_NAME]: `[${shopUrlName}] - ${window.location.href}`,
        [gaParameters.OPEN_TYPE]: 'pypec'
      });
    } else {
      GaHelper.pushEvent('close', {
        [gaParameters.SHOP_URL_NAME]: `[${shopUrlName}] - ${window.location.href}`,
        [gaParameters.CLOSE_TYPE]: 'pypec'
      });
      this.$emit('send-close-popup-ga-event');
    }
  }
}
