import browser from "../nameSpace";
import ErrorTracker from "../../ErrorTracker";

class StorageSync {
  public get(key: string) {
    console.debug('Storage sync - get: ', key);
    return browser.storage.sync.get(key)
      .then((items: any) => {
        if (browser.runtime.lastError) {
          ErrorTracker.trackError(browser.runtime.lastError.message);
        } else {
         return items[key];
        }
      })
      .catch((error) => {
        ErrorTracker.trackError(error);
      });
  }

  public set(object: object, callback?: Function) {
    console.debug('Storage sync - set: ', object);
    browser.storage.sync.set(object)
      .then(() => {
        if (browser.runtime.lastError) {
          ErrorTracker.trackError(browser.runtime.lastError.message);
        } else if(typeof callback === 'function') {
          callback();
        }
      })
      .catch((error) => {
        ErrorTracker.trackError(error);
      });
  }

  public remove(key: string) {
    console.debug('Storage sync - remove: ', key);
    browser.storage.sync.remove(key);
  }

  public getCollection(keys: string[]) {
    return new Promise<any[]>((resolve, reject) => {
      browser.storage.sync.get(keys, (items: any[]) => {
        if (browser.runtime.lastError) {
          reject(browser.runtime.lastError.message);
        } else {
          resolve(items);
        }
      });
    });
  }
}

export default new StorageSync();