import Message from "../../../modules/chrome/message/MessageContent";
import {messageName} from "../../../modules/constants";

const state = {
  locale: ''
};

const getters = {
  getLocale: state => {
    return state.locale;
  }
};

const mutations = {
  setLocale: (state, payload) => {
    state.locale = payload;
    setBackgorundLocale(payload);
  }
};

const setBackgorundLocale = (locale) => {
  Message.send((messageRequest) => {
    messageRequest
      .setType(messageName.SET_LOCALE)
      .setAsync(true)
      .setValue('locale', locale)
  });
};


export default {
  namespaced: true,
  state,
  getters,
  mutations
};