const metricURL: string = 'metric.picodi.com';
let splitURL: Array<string>;

export default {
  isCorrectCashbackRedirect(url: string, locale: string): boolean {
    this.splitUrl(url);
    return this.isMetric() && this.isCurrentLocale(locale) && this.isCashbackRedirect();
  },
  splitUrl(url: string) {
    splitURL = url.split('?')[0].split('/');
  },
  isMetric(): boolean {
    return splitURL[2] === metricURL;
  },
  isCurrentLocale(locale: string): boolean {
    return splitURL[3] === locale;
  },
  isCashbackRedirect(): boolean{
    return this.isOfferRedirect();
  },
  isShopRedirect(): boolean {
    return splitURL[4] === 'cr' && splitURL[5] === 's';
  },
  isOfferRedirect(): boolean {
    return splitURL[4] === 'cr';
  },
  getShopId(url: string, locale: string): string {
    if(this.isCorrectCashbackRedirect(url, locale)){
      return (this.isShopRedirect() ? `${splitURL[6]}s` : `${splitURL[5]}o`);
    }else{
      return null;
    }
  }
}