class Shop {
  public shopId: number;
  public shopName: string;
  public shopUrlName: string;
  public confirmationTime: string;
  public maxConfirmationTime: string;
  public versionRedirect: number;
  public cashbackTermsAndConditions: string;
  public fpoBonusTermsAndConditions: string;
  public premiumBonusTermsAndConditions: string;
  public cashbackDesc: string;
  public fpoCashbackDesc: string | null;
  public premiumCashbackDesc: string | null;
  public codeNumber: string;
  public promoNumber: string;
  public cashbackOfferId: number;
  public logo: string;
  public borderColor: string;
  public isCashbackEnabled: boolean;

  constructor(data: any) {
    this.shopId = data.shopId;
    this.shopName = data.shopName;
    this.shopUrlName = data.shopUrlName;
    this.confirmationTime = data.confirmationTime;
    this.maxConfirmationTime = data.maxConfirmationTime;
    this.versionRedirect = data.versionRedirect;
    this.cashbackTermsAndConditions = data.cashbackTermsAndConditions;
    this.fpoBonusTermsAndConditions = data.fpoBonusTermsAndConditions;
    this.premiumBonusTermsAndConditions = data.premiumBonusTermsAndConditions;
    this.cashbackDesc = data.cashbackDesc;
    this.fpoCashbackDesc = data.fpoCashbackDesc;
    this.premiumCashbackDesc = data.premiumCashbackDesc;
    this.codeNumber = data.codeNumber;
    this.promoNumber = data.promoNumber;
    this.cashbackOfferId = data.cashbackOfferId;
    this.logo = data.logo;
    this.borderColor = data.borderColor;
    this.isCashbackEnabled = data.isCashbackEnabled;
  }

  public getShopName (): string {
    return this.shopName;
  }

  public getShopUrlName (): string {
    return this.shopUrlName;
  }

  public isCashback(): boolean {
    return this.isCashbackEnabled;
  }
}

export default Shop