import Vue from "vue";
import AbstractSection from "./abstract/AbstractSection";
import Main from "../components/Main.vue";
import PopupHelper from "../service/PopupHelper";
import Message from "../../modules/chrome/message/MessageContent";
import MessageRequest from "../../modules/chrome/message/structures/MessageRequest";
import Offer from "./structures/Offer";
import Shop from "./structures/Shop";
import StorageSync from "../../modules/chrome/storage/StorageSync";
import cashbackActivations from "../../modules/cashbackActivations/index";
import ApiDataManager from "../service/ApiDataManager";
import DomainRecognize from "../service/DomainRecognize";
import ApiConfig from "../service/ApiConfig";
import CouponApplier from "./../caa/CouponApplier";
import store from "./../store/index";
import cashbackAvailableView from "../service/CashbackAvailableView";
import browser from "../../modules/chrome/nameSpace";
import isEmpty from "lodash/isEmpty";
import isObject from "lodash/isObject";
import picodiCookies from "../service/picodiCookies";
import CustomPrompt from "../service/CustomPrompt";
import {CustomPromptDetails} from "./structures/CustomPromptDetails";
import ShopsSuggestions from "./structures/ShopsSuggestions";
import ShopConfig from "./structures/shopConfig";
import {gaParameters, messageName, storageSyncKey} from "../../modules/constants";
import TestVariantManager from "../../modules/TestVariantManager";
import RootContent from "../RootContent";
import PopupVisibility from "../service/PopupVisibility";
import MessageRequestResponse from "../../modules/chrome/message/structures/MessageRequestResponse";
import GaHelper from "../service/GaHelper";
import Bonus from "../service/Bonus";
import ErrorTracker from "../../modules/ErrorTracker";

type VIEW_TYPES = 'OFFERS' | 'SETTINGS' | 'SEARCH' | 'START' | 'CART' | 'ACCOUNT' | 'CASHBACKPROMPT' | 'CASHBACKACTIVATED' | 'NOCASHBACKPROMPT' | 'NOTICEPROMPT' | 'CUSTOMPROMPT';

export default class PopupSection extends AbstractSection {
  private PopupHelper: PopupHelper;
  private domainRecognize: DomainRecognize;
  private shop: Shop;
  private shopConfig: ShopConfig;
  private couponApplier: CouponApplier;
  private trans: Object;
  private shopName: string;
  private shopUrlName: string;
  private picodiLoggedUserEmail: string;
  private activeView: VIEW_TYPES = 'SEARCH';
  private defaultView: VIEW_TYPES = 'SEARCH';
  private locale: string = this.getLocale();
  private offers: Array<Offer> = [];
  private shopsSuggestions: Array<ShopsSuggestions> = [];
  private codes: Array<string>;
  private isShowNoCashbackPrompt: boolean;
  private isSetLocale: boolean;
  private customPrompt: CustomPrompt;
  private customPromptDetails: CustomPromptDetails;

  constructor(rootContent: RootContent) {
   super(rootContent);
  }

  public render(){
    this.rootContent.getConfig().getStorageConfig()
      .then(storageConfig => {
        this.setPopupView(storageConfig)
          .then(() => this.renderVueApp(storageConfig, true))
          .then(() =>  this.initializeBackgroundListeners())
          .catch(() => this.renderVueApp(storageConfig, false));
        PopupVisibility.close();
      });
  }

  public static isEnabled(): boolean{
    return true;
  }

  public async renderVueApp(config: Object, showOffers = true): Promise<any> {
    this.PopupHelper = new PopupHelper('promo-popup');
    this.trans = this.rootContent.getTrans();
    const domainRecognize = this.rootContent.getDomainRecognize();
    const isSubsidExist = await picodiCookies.isSubsidExist(this.locale);
    const isRecognizeShop = domainRecognize.isRecognizeShop();
    const isShopConfigEnabled = domainRecognize.getIsShopConfigEnabled();
    const apiConfig = await ApiConfig.get(this.getLocale());
    const testAssignmentProps: object = await new TestVariantManager(this.locale).getProps();
    const bonus = new Bonus(this.locale);
    const isFpoEnabled = await bonus.isFPO();
    const firstPurchaseBonusExpireAt = await bonus.getFPBEA();

    Vue.mixin({
      methods: {
        // @ts-ignore
        trans: (key: string) => this.trans[key] ?? key,
        apiConfig: (key: string) => apiConfig.config[key] ?? null,
        apiShopList: () => apiConfig.shopList,
        apiCategoryList: () => apiConfig.categoryList,
        setConfiguration: (value: Object) => this.rootContent.setConfiguration(value)
      }
    });

    Vue.directive('focus', {
      inserted: (element: HTMLElement) => {
        element.focus()
      }
    });

    new Vue({
      el: this.PopupHelper.shadowRoot.querySelector('#promo-popup'),
      store,
      render: (h: Function) => h(Main, {
        props: {
          locale: this.getLocale(),
          offers: showOffers && !isEmpty(this.offers) ? this.offers : [],
          shop: showOffers && !isEmpty(this.shop) ? this.shop : {},
          customPromptDetails: !isEmpty(this.customPromptDetails) ? this.customPromptDetails : {},
          codes: !isEmpty(this.codes) ? this.codes : [],
          shopsSuggestions: !isEmpty(this.shopsSuggestions) ? this.shopsSuggestions : [],
          modalType: this.couponApplier ? this.couponApplier.status : null,
          shopName: this.shopName,
          shopUrlName: this.shopUrlName,
          activeView: this.activeView,
          defaultView: this.defaultView,
          couponApplier: this.couponApplier,
          customPrompt: this.customPrompt,
          loggedUserDataEmail: this.picodiLoggedUserEmail,
          isShowNoCashbackPrompt: this.isShowNoCashbackPrompt,
          config: config,
          isSubsidExist: isSubsidExist,
          isRecognizeShop: isRecognizeShop,
          isShopConfigEnabled: isShopConfigEnabled,
          isFpoEnabled: isFpoEnabled,
          firstPurchaseBonusExpireAt: firstPurchaseBonusExpireAt,
          ...testAssignmentProps
        }
      })
    });
  }

  public async setPopupView(config: Object): Promise<boolean> {
    const tab: browser.tabs.Tab = this.rootContent.getTab();
    this.domainRecognize = this.rootContent.getDomainRecognize();
    this.picodiLoggedUserEmail = await picodiCookies.getUsAddonData(this.locale);
    this.isSetLocale = await this.isUserHasLocaleSet();

    return this.setViewChooseLanguage() ||
      await this.setViewIfDomianNotRecognize() ||
      await this.getDataFromApi(this.domainRecognize, config) ||
      await this.couponApplierInitialize(tab) ||
      await this.setViewIfCashbackShop() ||
      await this.setViewIfNoCashbackPrompt() ||
      this.setViewIfNotCashbackShop()
  }

  public getShopUrlName(): string{
    return this.shopUrlName;
  }

  private setViewChooseLanguage(): boolean{
    if(!this.isSetLocale){
      this.setViews('START', 'START');
      return true;
    }
    return false;
  }

  private async setViewIfDomianNotRecognize(): Promise<boolean>{
    if(!this.domainRecognize.isRecognizeShop()){
      this.customPrompt = new CustomPrompt(this.locale, this.domainRecognize);
      if(await this.customPrompt.isTriggeringUrl()){
        this.customPromptDetails = await this.customPrompt.getDetails();
        this.setViews('CUSTOMPROMPT', 'CUSTOMPROMPT');
        return true;
      }

      if(!this.isLoggedUser()){
        this.setViews('ACCOUNT', 'SEARCH');
      }else{
        this.setViews('SEARCH', 'SEARCH');
      }
      return true;
    }
    return false;
  }

  private async setViewIfCashbackShop(){
    if(!this.shop.isCashback() || isEmpty(this.offers)){
      return false;
    }

    if(!this.domainRecognize.getIsShopConfigEnabled()){
      this.setViews('OFFERS', 'OFFERS');
      return true;
    }

    const isCashbackPromptViewToDisplay = await cashbackAvailableView.isShowInShop(this.shop.shopId);
    const isShopCashbackActivated = await cashbackActivations.isActiveCashback(this.shop, this.offers);
    const isNoticePromptWasOnceVisible = await this.isNoticePromptWasOnceVisible(isShopCashbackActivated);

    if(this.shop && isShopCashbackActivated && !isNoticePromptWasOnceVisible){
      this.setViews('NOTICEPROMPT', 'NOTICEPROMPT');
      return true;
    }else if(this.shop && this.shopConfig.isShowCashbackPrompt() && !isShopCashbackActivated && isCashbackPromptViewToDisplay) {
      this.setViews('CASHBACKPROMPT', 'CASHBACKPROMPT');
      return true;
    }
    this.setViews('OFFERS', 'OFFERS');
    return true;
  }

  private setViewIfNotCashbackShop(): boolean{
    if(!this.isLoggedUser()){
      this.setViews('ACCOUNT', 'OFFERS');
    }else if(!isEmpty(this.offers)){
      this.setViews('OFFERS', 'OFFERS');
    }else{
      this.setViews('SEARCH', 'SEARCH');
    }
    return true;
  }

  private async setViewIfNoCashbackPrompt(): Promise<boolean>{
    if(!this.shopConfig.isShowNoCashbackPrompt() || isEmpty(this.shopsSuggestions)){
      return false;
    }

    const isNoCashbackPromptViewToDisplay = await cashbackAvailableView.isShowInShop(this.shop.shopId);
    this.shopUrlName = this.shop.getShopUrlName();

    if(!isEmpty(this.shopsSuggestions) && isNoCashbackPromptViewToDisplay){
      this.isShowNoCashbackPrompt = isNoCashbackPromptViewToDisplay;
      this.setViews('NOCASHBACKPROMPT', 'NOCASHBACKPROMPT');
    }else if(!isEmpty(this.offers)){
      this.setViews('OFFERS', 'OFFERS');
    }else{
      this.setViews('SEARCH', 'SEARCH');
    }
    return true;
  }

  private setOffersCounter(): void {
    if(isEmpty(this.offers)) return;
    Message.send((messageRequest: MessageRequest) => {
      messageRequest
        .setType(messageName.OFFERS_COUNTER)
        .setValue('offersCount', this.offers.length);
    });
  }

  private isLoggedUser(): boolean {
    return !isEmpty(this.picodiLoggedUserEmail);
  }

  private async getDataFromApi(domainRecognize: DomainRecognize, config: object): Promise<boolean> {
    try {
      const shopData = await this.fetchShopData(domainRecognize);
      this.shopConfig = new ShopConfig(shopData.config);
      if(!this.shopConfig.isEnableInAddOn()){
        this.setViewIfDomianNotRecognize();
        return false;
      }

      this.shop = new Shop(shopData.shopData);
      this.shopName = isObject(shopData.shop) ? this.shop.getShopName() : shopData.shop;
      this.shopUrlName = this.shop.getShopUrlName();
      this.codes = shopData.codes;

      if(!isEmpty(shopData.offers)){
        await this.setOffers(shopData.offers, config);
      }

      if(!isEmpty(shopData.shops)){
        await this.setShopsSuggestions(shopData.shops);
      }

      this.setOffersCounter();
      return false;
    }catch (error){
      if(!this.isLoggedUser()){
        this.setViews('ACCOUNT', 'SEARCH');
      }else{
        this.setViews('SEARCH', 'SEARCH');
      }
      ErrorTracker.trackError(error);
      return true;
    }
  }

  private async fetchShopData(domainRecognize: DomainRecognize){
    try {
      const shopUrlNameRecognize = domainRecognize.getRecognizeShopUrlName();
      const shop = new ApiDataManager('api.get.shop', {locale: this.getLocale(), shopUrlName: shopUrlNameRecognize}, 1/12);
      return await shop.getData(['shopUrlName', shopUrlNameRecognize]);
    }catch (error){
      ErrorTracker.trackError(error);
      //throw new Error(error);
    }
  }

  private setViews(activeView: VIEW_TYPES, defaultView: VIEW_TYPES): void{
    this.activeView = activeView;
    this.defaultView = defaultView;
  }

  private async setOffers(offers: Array<Offer>, config: any) {
    await Promise.all(offers.map(async (offer: Offer) => {
      if(offer.type === 'PROMO' && !('suggest_deals' in config)) {
        this.offers.push(new Offer(offer));
      }

      if(offer.type === 'CODE' && !('promo_codes' in config)) {
        this.offers.push(new Offer(offer));
      }
    }));
  }

  private async setShopsSuggestions(shops: Array<ShopsSuggestions>){
    await Promise.all(shops.map(async (shopsSuggestions: ShopsSuggestions) => {
      this.shopsSuggestions.push(new ShopsSuggestions(shopsSuggestions));
    }));
  }

  private async couponApplierInitialize(tab: browser.tabs.Tab): Promise<boolean>{
    if(!isEmpty(this.codes) && this.shopConfig.isShowCaa()){
      this.couponApplier = new CouponApplier(this.locale, this.shopName);
      if(await this.couponApplier.initialize(this.codes, tab.id)){
        this.setViews('CART', 'CART');
        return true;
      }else{
        return false
      }
    }
    return false;
  }

  private isUserHasLocaleSet(): Promise<boolean>{
    return new Promise((resolve) => {
      StorageSync.get(storageSyncKey.IS_INSTALLED)
        .then((isInstalled) => {
          if(typeof isInstalled === 'undefined'){
            resolve(false);
          }else{
            resolve(true);
          }
        })
    })
  }

  private async isNoticePromptWasOnceVisible(isActivatedCashback: boolean){
    return new Promise((resolve) => {
      StorageSync.get(storageSyncKey.IS_NOTICE_PROMPT_WAS_ONCE_VISIBLE)
        .then((isNoticePromptWasOnceVisible) => {
          if(typeof isNoticePromptWasOnceVisible === 'undefined'){
            resolve(false);
          }else if(!isNoticePromptWasOnceVisible[this.shop.shopId]){
            if(isActivatedCashback) {
              isNoticePromptWasOnceVisible[this.shop.shopId] = true;
            }
            resolve(false);
          }else if(!isActivatedCashback){
            delete isNoticePromptWasOnceVisible[this.shop.shopId];
            resolve(false);
          }else {
            resolve(true);
          }
        });
    })
  }

  private initializeBackgroundListeners() {
    Message.addListener(messageName.CLICK_EXTENSION_ICON, (messageResponse: MessageRequestResponse) => {
      const shopUrlName = this.getShopUrlName() ?? 'n/a';

      if (typeof this.rootContent.getConfiguration() === 'undefined') {
        this.rootContent.setConfiguration(messageResponse.getValue('configuration'));
      }

      PopupVisibility.toggle();
      PopupVisibility.check()
        .then(() => {
          GaHelper.pushEvent('close', {
            [gaParameters.SHOP_URL_NAME]: `[${shopUrlName}] - ${window.location.href}`,
            [gaParameters.CLOSE_TYPE]: 'toolbar'
          });
        })
        .catch(() => {
          GaHelper.pushEvent('open', {
            [gaParameters.SHOP_URL_NAME]: `[${shopUrlName}] - ${window.location.href}`,
            [gaParameters.OPEN_TYPE]: 'toolbar'
          });
        })
    })

    Message.addListener(messageName.OPEN_EXTENSION_POPUP, () => {
      PopupVisibility.toggle();
      GaHelper.pushEvent('open', {
        [gaParameters.SHOP_URL_NAME]: `[n/a] - ${window.location.href}`,
        [gaParameters.OPEN_TYPE]: 'button_lp_welcome'
      });
    })
  }
}