import axios from "axios";
import {AxiosResponse} from "axios/index";
import browser from "../../modules/chrome/nameSpace";

export default class PopupHelper {
  element: HTMLElement;
  shadowRoot: ShadowRoot;

  constructor(id: string) {
    this.element = document.createElement('div');
    this.element.style.cssText = 'all: initial;';
    this.shadowRoot = this.element.attachShadow({mode: 'closed'});
    this.shadowRoot.innerHTML = `<div id="${id}"></div>`;
    this.addStyleTag();
    this.addElement();
  }

  public fetchCss() {
    const url = browser.runtime.getURL('content.css');
    return axios.get(url)
      .then((response: AxiosResponse) => response.data)
  }

  public addStyleTag(): void {
    let link = document.createElement('style');
    this.fetchCss()
      .then((data: string) => {
        link.innerHTML = data;
        this.shadowRoot.appendChild(link);
      })
  }

  public addElement(): void {
    const bodyElement: HTMLElement = document.querySelector('body');
    bodyElement.after(this.element);
  }
}
