import StorageSync from "../modules/chrome/storage/StorageSync";
import {storageSyncKey} from "../modules/constants";
import countriesCodeMap from "../static/data/countiesCodeMap/countriesCodeMap";
import languagesMap from "../static/data/languagesMap/languagesMap";
import browser from "../modules/chrome/nameSpace";
import {getCookiesFromRegex, getLastCookieKey} from "../modules/functions";
import ErrorTracker from "../modules/ErrorTracker";

class Locale{
  private readonly defaultLocale: string;
  private locale: string;

  constructor(){
    this.defaultLocale = 'au';
  }

  public async getLocale(): Promise<string>{
    try{
      const storageLocale = await this.getStorageLang();
      if(storageLocale && this.isApiExist(storageLocale)){
        return this.locale = storageLocale;
      }

      const cookieLocale = await this.getCookieLang()
      if(cookieLocale && this.isApiExist(cookieLocale)){
        this.setStorageLang(cookieLocale);
        this.setStorageIsInstalled();
        return this.locale = cookieLocale;
      }

      const browserLocale = this.getBrowserLang();
      if(browserLocale){
        this.setStorageLang(browserLocale);
        this.setStorageIsInstalled();
        return this.locale = browserLocale;
      }
      return this.locale = this.defaultLocale;
    }
    catch (error) {
      ErrorTracker.trackError(error);
    }
  }

  private async getCookieLang(): Promise<string | false>{
    try {
      const stCookies = await getCookiesFromRegex("^picodi_[a-z]{2}_st$");
      if(stCookies){
        const lastCookie = getLastCookieKey(stCookies);
        if (lastCookie.name) {
          return lastCookie.name.split('_')[1];
        }else{
          return false;
        }
      }else{
        return false;
      }
    }
    catch (error) {
      ErrorTracker.trackError(error);
    }
  }

  private getStorageLang(): Promise<string | false> {
    return new Promise((resolve) => {
      StorageSync.get('locale')
        .then(locale => {
          if (typeof locale !== 'undefined') {
            resolve(locale);
          } else {
            resolve(false);
          }
        })
        .catch(() => {
          ErrorTracker.trackError(`Unable to get 'locale' from storage sync`)
          //throw new Error(`Unable to get 'locale' from storage sync`);
        })
    })
  }

  private getBrowserLang(): string | undefined {
    const lang = browser.i18n.getUILanguage();
    const apiMarketMap = this.getApiMarketMap();

    if(apiMarketMap && apiMarketMap.hasOwnProperty(lang)){
      return apiMarketMap[lang];
    }
  }

  private getApiMarketMap(): { [key: string]: string } {
    return languagesMap;
  }

  private setStorageLang(locale: string): void{
    StorageSync.set({[storageSyncKey.LOCALE]: locale})
  }

  private setStorageIsInstalled(): void{
    StorageSync.set({[storageSyncKey.IS_INSTALLED]: true});
  }

  private isApiExist(locale: string): boolean {
    return !!countriesCodeMap[locale];
  }
}

export default new Locale();