













































  import Component from "vue-class-component";
  import {Prop} from "vue-property-decorator";
  import AbstractOfferSection from "./AbstractOfferSection.vue";
  import ShopSearch from "../sections/structures/ShopSearch";
  import GaHelper from "../service/GaHelper";
  import ApiDataManager from "../service/ApiDataManager";
  import {gaParameters} from "../../modules/constants";

@Component
export default class Search extends AbstractOfferSection {
  @Prop(Function) dataError!: boolean;
  @Prop(Boolean) isFpoEnabled!: boolean;

  shops: Array<ShopSearch> = [];
  filteredShops: Array<ShopSearch> = this.shops;
  searchValue: string = '';
  messageText: string = this.trans('addon.search.no-shops');
  lastSearch: string = '';
  locale: string = this.$store.getters[('locale/getLocale')];
  timeout: ReturnType<typeof setTimeout>;

  inputSearchListener(): void {
    this.filteredShops = this.shops.filter(shop => shop.name.toLowerCase().includes(this.searchValue.toLowerCase()));
    clearTimeout(this.timeout);
    this.timeout = setTimeout(() => {
      if(this.lastSearch !== this.searchValue && this.searchValue !== '') {
        this.lastSearch = this.searchValue;
        GaHelper.pushEvent('keyup', {
          [gaParameters.PAGE_TITLE]: 'search',
          [gaParameters.PAGE_LOCATION]: `/${this.locale}/search`,
          [gaParameters.SEARCH_VALUE]: this.searchValue
        });
      }
    }, 2000)
  }

  shopClick(shopUrlName: string): void {
    this.$emit('change-view', 'OFFERS', shopUrlName);
  }

  fetchShops(): void {
    const search = new ApiDataManager('api.search', {locale: this.locale})
    search.getData()
      .then((data) => {
        this.shops = this.filteredShops = data.shops;
        if(!this.shops.length) {
          this.messageText = this.trans('addon.search.no-shops');
        }
      })
      .catch(() => {
        this.messageText = this.trans('addon.error.server');
      })
  }

  created(): void {
    this.fetchShops();
  }

  mounted(): void {
    GaHelper.pushPageView({
      [gaParameters.PAGE_TITLE]: 'search',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/search`
    });
  }
}
