


















































import Component from "vue-class-component";
import {Prop} from "vue-property-decorator";
import {mapActions, mapGetters} from "vuex";

import AbstractOfferSection from "./AbstractOfferSection.vue";
import Popup from "./Popup.vue";
import PromoIcon from "./PromoIcon.vue";
import CaaModal from "./caa-views/caa-modal/CaaModal.vue";
import Offer from "../sections/structures/Offer";
import ShopsSuggestions from "../sections/structures/ShopsSuggestions";
import Config from "../../backend/Config";
import MessageRequest from "../../modules/chrome/message/structures/MessageRequest";
import HttpResponse from "../../modules/api/structures/HttpResponseContent";
import CouponApplier from "./../caa/CouponApplier";
import CustomPrompt from "./../service/CustomPrompt";
import MessageRequestResponse from "../../modules/chrome/message/structures/MessageRequestResponse";
import StorageSync from "../../modules/chrome/storage/StorageSync";
import Message from "../../modules/chrome/message/MessageContent";
import GaHelper from "../service/GaHelper";
import PopupVisibility from "../service/PopupVisibility";
import {gaParameters, messageName, storageSyncKey, testVariant} from "../../modules/constants";
import {Status} from "@mobile-rewards/caa-module";
import browser from "../../modules/chrome/nameSpace";
import ErrorTracker from "../../modules/ErrorTracker";

const {
  UNAVAILABLE,
  STOP,
  SUCCESS,
  FAIL,
  PROCESS,
  INIT,
  STARTING,
  FINISHING,
  PAUSE,
  RESTORING,
} = Status;

@Component({
  computed: {
    ...mapGetters({
      getActiveView: 'views/getActive'
    })
  },
  methods: {
    ...mapActions({
      setActiveView: 'views/setActiveView'
    })
  },
  components: {CaaModal, Popup, PromoIcon}
})
export default class Main extends AbstractOfferSection  {
  @Prop(String) locale!: string;
  @Prop(String) activeView!: string;
  @Prop(String) defaultView!: string;
  @Prop(String) shopName!: string;
  @Prop(String) shopUrlName!: string;
  @Prop(String) loggedUserDataEmail !: string;
  @Prop(Array) offers!: Array<Offer>;
  @Prop(Array) codes!: Array<string>;
  @Prop(Array) shopsSuggestions !: Array<ShopsSuggestions>;
  @Prop(Object) shop!: Object;
  @Prop(Object) config!: Object;
  @Prop(Object) customPromptDetails!: object;
  @Prop(CouponApplier) couponApplier!: CouponApplier;
  @Prop(CustomPrompt) customPrompt!: CustomPrompt;
  @Prop(Boolean) isSubsidExist!: boolean;
  @Prop(Boolean) isRecognizeShop!: boolean;
  @Prop(Boolean) isShopConfigEnabled!: boolean;
  @Prop(Boolean) isShowNoCashbackPrompt!: boolean;
  @Prop(Boolean) isAssignedToTheTestA!: boolean;
  @Prop(Boolean) isAssignedToTheTestB!: boolean;
  @Prop(Boolean) isFpoEnabled!: boolean;
  @Prop(Number) firstPurchaseBonusExpireAt!: number;

 // @Prop(Function) setConfiguration!: Function;

  searchShopName: string = '';
  searchOffers: Array<Offer> = [];
  isPopupActive: boolean = false;
  modalType: string = '';
  bestCoupon: object;
  totalPriceInPage: number;
  currentCoupon: string = '';
  setActiveView!: (msg) => any;

  caaInitialize() {
    if(typeof this.couponApplier !== 'undefined' && typeof this.couponApplier.caa !== 'undefined'){
      this.couponApplier.caa.on('update:status', (status) => {
        if(status){
          if(status === SUCCESS){
            this.couponApplier.caaAnalyticsHelper.sendAnalytics(status.toUpperCase());
            this.couponApplier.picometricHelper.pushEvent(this.locale, 'CAA_SUCCESS', JSON.stringify({ shopUrlName: this.shopUrlName, numberOfCoupons: this.couponApplier.coupons.length, totalPriceInPage: this.totalPriceInPage, bestCoupon: this.bestCoupon }), window.location.href);
            this.gaSendPageViewCaa('caa_success', 'success');
          }
          if(status === FAIL){
            this.couponApplier.caaAnalyticsHelper.sendAnalytics(status.toUpperCase());
            this.couponApplier.picometricHelper.pushEvent(this.locale, 'CAA_FAIL', JSON.stringify({ shopUrlName: this.shopUrlName, numberOfCoupons: this.couponApplier.coupons.length }), window.location.href);
            this.gaSendPageViewCaa('caa_no_success', 'no_success');
          }

          this.couponApplier.status = status;
          this.couponApplier.getTabCaaStatusFromStorage()
            .then((caa) => {
              if(caa !== PROCESS){
                this.couponApplier.removeCaaStatusFromStorage();
              }
              return false;
            })
            .catch(() => {
              if(status === PROCESS){
                this.setSyncStorageFromCaaProgress();
              }
              this.updateStatusCaa(status);
            });
        }
      });

      this.couponApplier.caa.on('update:result', (result) => {
        this.couponApplier.result = result;
        if (this.couponApplier.caa.status !== INIT) {
          const totalPriceInPage = this.couponApplier.caa.total;
          const bestCoupon = result && result.bestCoupon;


          if (bestCoupon && bestCoupon.discount > 0) {
            this.bestCoupon = bestCoupon;
            this.totalPriceInPage = totalPriceInPage;
            this.setModalType('SUCCESS');

            StorageSync.get(storageSyncKey.LAST_CAA_SUCCESS).then(lastCaaSuccess => {
              if (typeof lastCaaSuccess === 'undefined') {
                StorageSync.set({[storageSyncKey.LAST_CAA_SUCCESS]:
                  {
                    'totalPrice': this.totalPriceInPage,
                    'discount': this.bestCoupon.discount,
                    'shopName': this.shopName,
                    'timestamp': Date.now()
                  }
                })
              }
            })
            .catch((e) => {
              ErrorTracker.trackError(`Unable to get '${storageSyncKey.LAST_CAA_SUCCESS}' from storage - ${e}`)
            });
          } else if(this.couponApplier.caa.status === FINISHING) {
            this.setModalType('FAILED');
          }
          this.saveCaaClosureInformation();
        }
      });

      this.couponApplier.caa.on('update:processIndex', (processIndex) => {
        const coupons = this.couponApplier.coupons;
        this.couponApplier.progress = ((processIndex + 1) / (coupons.length))*100;
        this.couponApplier.currentCoupon = coupons[processIndex] && coupons[processIndex].code;
        this.currentCoupon = coupons[processIndex] && coupons[processIndex].code;

        if(processIndex !== 0 && processIndex < coupons.length){
          this.setSyncStorageFromCaaProgress();
        }
      });

      this.setCaaStatusFromStorage();

      if(this.couponApplier.status){
        if (this.couponApplier.status !== PROCESS) {
          this.couponApplier.removeCaaStatusFromStorage();
        }
      }

      if(this.couponApplier.status !== UNAVAILABLE) {
        this.couponApplier.caaInit();
      }
    }
  }

  updateStatusCaa(status){
    switch (status) {
      case INIT:
        this.setActiveView('CART');
        this.checkIsCartCanceled();
        break;
      case PROCESS:
        this.gaSendPageViewCaa('caa_testing', 'testing');
        this.setModalType('PROGRESS');
        break;
      case SUCCESS:
        this.setActiveView('CART');
        break;
      case FAIL:
        this.setActiveView('CART');
        break;
      case FINISHING:
        this.setActiveView('CART');
        break;
      case PAUSE:
        this.couponApplier.removeCaaStatusFromStorage();
        break;
      case UNAVAILABLE:
        this.couponApplier.removeCaaStatusFromStorage();
        this.setActiveView('OFFERS');
        break;
      default:
        this.setActiveView('CART');
    }
  }

  setSyncStorageFromCaaProgress(){
    if(this.couponApplier.currentCoupon && this.couponApplier.progress){
      const caaStorageProgress = {'status': 'process', 'progress': this.couponApplier.progress,  'currentCoupon':  this.couponApplier.currentCoupon};
      this.couponApplier.saveCaaStatusInStorage(caaStorageProgress);
    }
  }

  setCaaStatusFromStorage(){
    this.couponApplier.getTabCaaStatusFromStorage()
      .then((caa) => {
          this.currentCoupon = caa.currentCoupon;
          this.couponApplier.currentCoupon = caa.currentCoupon;
          this.couponApplier.progress = caa.progress;
          this.updateStatusCaa(caa.status);
      })
      .catch(e => {
        ErrorTracker.trackError(`Unable to get caa status from storage - ${e}`)
      });
  }

  setModalType(modalType: 'PROGRESS'|'SUCCESS'|'FAILED'|'') {
    this.modalType = modalType;
  }

  setSearchOffers(val: Array<Offer>) {
    this.searchOffers = val;
  }

  setSearchShopName(val: string) {
    this.searchShopName = val;
  }

  togglePopup() {
    PopupVisibility.toggle();
  }

  showPromoIcon() {
    return (this.isShopConfigEnabled && this.offers.length) || (this.activeView === 'NOCASHBACKPROMPT' && this.isShowNoCashbackPrompt) || this.activeView === 'CUSTOMPROMPT';
  }

  setApiAddonConfig(changeLocale: string = null): void {
    const config = new Config();
    config.getStorageConfig()
      .then((addonConfig) => {
        if(JSON.stringify(addonConfig) !== JSON.stringify(this.config) || changeLocale) {
          addonConfig = Object.assign({'market': changeLocale ? changeLocale : this.locale}, addonConfig);
          Message.send((messageRequest: MessageRequest) => {
            messageRequest
              .setType('api.set.config')
              .setAsync(true)
              .setValue('locale', this.locale)
              .setValue('configuration', JSON.stringify(addonConfig))
              .setHttpResponseCallback((httpResponse: HttpResponse) => {
                if (httpResponse.isSuccessful()) {
                  StorageSync.set({[storageSyncKey.ADDON_CONFIG]: addonConfig});
                  this.config = addonConfig;
                  this.setConfiguration(addonConfig);
                }
              });
          });
        }
      })
      .catch((e) => {
        ErrorTracker.trackError(`Unable to get config from storage - ${e}`)
      })
  }

  checkIsCartCanceled() {
    StorageSync.get(storageSyncKey.CART_CANCELED)
      .then((cartCanceled) => {
        const currentTime = new Date().getTime();
        if(cartCanceled) {
          if(!cartCanceled[this.shopUrlName] || cartCanceled[this.shopUrlName] < currentTime) {
              this.togglePopup();
          }
        }else{
          this.togglePopup();
        }
      });
  }

  saveCaaClosureInformation(): void {
    const expireDate = new Date().getTime() + (60*60*1000);
    StorageSync.get(storageSyncKey.CART_CANCELED)
      .then((cartCanceled) => {
        const newCartCanceled = {...cartCanceled};
        newCartCanceled[this.shopUrlName] = expireDate;
        StorageSync.set({[storageSyncKey.CART_CANCELED]: newCartCanceled});
      });
  }

  sendClosePopupGaEvent(): void {
    this.activeView = this.$store.getters[('views/getActive')];

    if(this.activeView === 'CASHBACKPROMPT'){
      GaHelper.pushEvent('close', {
        [gaParameters.PAGE_TITLE]: 'cashback_prompt',
        [gaParameters.PAGE_LOCATION]: `/${this.locale}/cb/${this.shopUrlName}/notify`,
        [gaParameters.SHOP_URL_NAME]: this.shopUrlName,
        [gaParameters.TEST_VARIANT]: this.isAssignedToTheTestB ? testVariant.B : testVariant.A
      });
    }

    if(this.activeView === 'NOCASHBACKPROMPT'){
      GaHelper.pushEvent('close', {
        [gaParameters.PAGE_TITLE]: 'no-cashback_prompt',
        [gaParameters.PAGE_LOCATION]: `/${this.locale}/no-cb/${this.shopUrlName}/notify`,
        [gaParameters.SHOP_URL_NAME]: this.shopUrlName
      });
    }

    if(this.activeView === 'CUSTOMPROMPT'){
      GaHelper.pushEvent('close', {
        [gaParameters.PAGE_TITLE]: 'custom_prompt',
        [gaParameters.PAGE_LOCATION]: `/${this.locale}/custom-prompt`,
        [gaParameters.SHOP_URL_NAME]: this.customPrompt.getTriggerUrl()
      });
    }
  }

  gaSendPageViewCaa(pageTitle: string, type: string): void {
    GaHelper.pushPageView({
      [gaParameters.PAGE_TITLE]: pageTitle,
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/caa/${this.offers[0].shopUrlName}/${type}`,
      [gaParameters.SHOP_URL_NAME]: this.offers[0].shopUrlName
    });
  }

  mounted(): void{
    if(this.activeView === 'CUSTOMPROMPT' || this.activeView === 'CASHBACKPROMPT' || this.activeView === 'NOTICEPROMPT'){
      this.isPopupActive = true;
      this.togglePopup();
    }

    if(this.activeView === 'NOCASHBACKPROMPT' && this.isShowNoCashbackPrompt){
      this.isPopupActive = true;
      this.togglePopup();
    }

    if(this.activeView !== 'START' && this.activeView !== 'SEARCH' && this.activeView !== 'ACCOUNT' && this.activeView !== 'NOCASHBACKPROMPT'){
      this.caaInitialize();
    }

    this.$store.commit('offers/setOffers', this.offers);
    this.$store.commit('shop/setShop', this.shop);
    this.$store.commit('customPrompt/setCustomPrompt', this.customPromptDetails);
    this.$store.commit('locale/setLocale', this.locale);
    this.$store.commit('views/setActive', this.activeView);
    this.$store.commit('views/setDefault', this.defaultView);

    Message.addListener(messageName.TAB_ON_UPDATE, (messageResponse: MessageRequestResponse) => {
      const tab = messageResponse.getValue('tab');
      if(typeof this.couponApplier !== 'undefined'){
        this.couponApplier.removeCaaStatusFromStorage();
        this.couponApplier.initialize(this.codes, tab.id)
          .then((isRecognize) => {
            if(isRecognize){
              this.caaInitialize();
            }
          });
      }
    });

    browser.storage.onChanged.addListener((storage) => {
      if(storage.isPopupVisible){
        PopupVisibility.check()
          .then((isVisible) => {
            this.isPopupActive = isVisible;
          })
          .catch(() => {
            this.isPopupActive = false;
          });
      }
    })
  }

}
