import Message from "../../modules/chrome/message/MessageContent";
import MessageRequest from "../../modules/chrome/message/structures/MessageRequest";
import {messageName} from "../../modules/constants";

class PopupVisibility {
  public toggle(): void {
    Message.send((messageRequest: MessageRequest) => {
      messageRequest
        .setType(messageName.CHANGE_POPUP_VISIBILITY)
    });
  }

  public close(): void {
    Message.send((messageRequest: MessageRequest) => {
      messageRequest
        .setType(messageName.CLOSE_POPUP_VISIBILITY)
    });
  }

  public check(): Promise<boolean> {
    return new Promise((resolve, reject) => {
      Message.send((messageRequest: MessageRequest) => {
        messageRequest
          .setType(messageName.CHECK_POPUP_VISIBILITY)
          .setAsync(true)
          .setResponseCallback((tabId: any) => {
            if(!tabId || typeof tabId === 'undefined'){
              reject();
            }else{
              resolve(tabId);
            }
          });
      });
    })
  }
}

export default new PopupVisibility();