import forEach from "lodash/forEach";

import Tracking from "../../backend/Tracking"
import Config from "../../backend/Config";
import ApiDataManager from "./ApiDataManager";
import ApiConfig from "./ApiConfig";
import resolve = browser.dns.resolve;
import isEmpty from "lodash/isEmpty";

export default class DomainRecognize {
  private readonly url: string;
  private readonly locale: string;
  private isRecognize: boolean;
  private isShopConfigEnabled: boolean;
  private hostUrlName: string;
  private shopUrlName: string;
  private shopGroup: number;
  private config: Config;
  private domains: {[index: string]: {group: number, urlName: string}};

  constructor(url: string, locale: string) {
    this.url = url;
    this.locale = locale;
    this.hostUrlName = this.getHostUrlName(url);
    this.config = new Config();
    this.isRecognize = false;
    this.isShopConfigEnabled = false;
  }

  public getRecognizeShopUrlName(): string{
    return this.shopUrlName;
  }

  public getRecognizeshopGroup(): number{
    return this.shopGroup;
  }

  public isRecognizeShopGroup() {
    return this.shopGroup == 1;
  }

  public isRecognizeShopWithoutCashbackGroup() {
    return this.shopGroup == 9;
  }

  public isRecognizeShop(){
    return this.isRecognize;
  }

  public getIsShopConfigEnabled(){
    return this.isShopConfigEnabled;
  }

  public async initRecognize(){
    await this.getDomains();
    if(this.domains !== undefined){
      this.setTracking();
      return await this.getIsRecognize();
    }
  }

  public async getIsRecognize(): Promise<boolean>{
    if((this.isRecognizeDomain() || this.isRecognizeSubDomain()) && this.urlNameExist()){
      const isConfigEnabled = await this.isConfigEnabled();
      if(this.isShopGroup()){
        this.shopUrlName = this.domains[this.hostUrlName]['urlName'];
        this.shopGroup = this.domains[this.hostUrlName]['group'];
        this.isRecognize = true;
        this.isShopConfigEnabled = isConfigEnabled;
        return true;
      }else{
        return false
      }
    }else{
      return false;
    }
  }

  public getUrlWithoutParameters(): string{
    const newUrl = new URL(this.url);
    return `${newUrl.hostname}${newUrl.pathname !== '/' ? newUrl.pathname : ''}`;
  }

  private urlNameExist(): boolean{
    return this.domains[this.hostUrlName].hasOwnProperty('urlName');
  }

  private isRecognizeDomain(): boolean{
    return this.hostUrlName in this.domains;
  }

  private isRecognizeSubDomain(): boolean{
    const hostUrlName = this.getMainPartOfDomain();
    if(hostUrlName in this.domains ){
      this.hostUrlName = hostUrlName;
      return true;
    }else{
      return false;
    }
  }

  private isShopGroup(): boolean{
    return this.domains[this.hostUrlName]['group'] == 1 || this.domains[this.hostUrlName]['group'] == 9;
  }

  private async isConfigEnabled(): Promise<boolean>{
    const storageConfig = await this.config.getConfig();
    if(typeof storageConfig === "undefined" || isEmpty(storageConfig)) return true;

    const apiConfig = await ApiConfig.get(this.locale);
    const { shopList } = apiConfig;
    if(typeof shopList === "undefined" || isEmpty(shopList)) return true;

    for await (const shop of shopList) {
      if(shop['urlName'] === this.domains[this.hostUrlName]['urlName'] &&
        ((`shop-${shop.id}` in storageConfig) || (`category-${shop.shopCategoryId}` in storageConfig))) {
        return false;
      }
    }

    return true;
  }

  private setTracking(): void{
    Tracking.setTracking(this.url, this.hostUrlName in this.domains);
  }

  private getMapDomainsObject(domains: Array<Object>): {[index: string]: {group: number, urlName: string}}{
    // todo w obiekcie domains mamy przesylane z api nadmiarowe dane (category, shopId i shopCategoryId)
    return Object.assign({}, ...domains.map(({domain, group, urlName}: { domain: string; group: number; urlName: string }) => ({[domain]: {group, urlName}})));
  }

  private getHostUrlName(url: string): string{
    return new URL(url).hostname.replace('www.', '')
  }

  private getMainPartOfDomain(): string{
    return this.hostUrlName.split('.').slice(1).join('.');
  }

  private getDomains(): Promise<void>{
    const domains = new ApiDataManager('api.domains', {locale: this.locale})
    return new Promise<void>(resolve => {
      return domains.getData()
        .then(data => {
          if (data.domains && data.domains.length > 0) {
            this.domains = this.getMapDomainsObject(data.domains);
            resolve();
          }
        })
    });
  }
}