






















import Vue from "vue";
import Component from "vue-class-component";
import GaHelper from "../../service/GaHelper";
import {accountRedirectType, gaParameters} from "../../../modules/constants";

@Component()
export default class Account extends Vue {
  locale: string = this.$store.getters[('locale/getLocale')];
  accountRedirectType = Object.freeze(accountRedirectType);

  redirectTab(options: {[index: string]: accountRedirectType}): void{
    this.$emit('redirect-tab', options);
  }

  mounted(){
    GaHelper.pushPageView({
      [gaParameters.PAGE_TITLE]: 'login_prompt',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/register`
    });
  }
}
