import NumberToCurrency from "../../modules/NumberToCurrency";
import {lastCaaSuccessStorage} from "../../modules/constants";

export default class ReviewPageHelper {
  numberToCurrency: NumberToCurrency;
  dom: HTMLDocument;
  data: lastCaaSuccessStorage;
  locale: string;
  trans: { [key: string]: string; };

  constructor(data: lastCaaSuccessStorage, locale: string, trans: { [key: string]: string; }) {
    this.dom = document;
    this.data = data;
    this.locale = locale;
    this.trans = trans;
    this.addElement();
  }

  private setLastApplicationTime(): void{
    const howManyHours = this.msToHours(Date.now()- this.data.timestamp);

    if(howManyHours < 24){
      this.dom.querySelector('.extension-review__time').textContent = this.trans['addon.review-page.header.recently'];
    }else if(howManyHours < 48){
      this.dom.querySelector('.extension-review__time').textContent = this.trans['addon.review-page.header.yesterday'];
    }else{
      this.dom.querySelector('.extension-review__time').textContent = `${this.hoursToDays(howManyHours)} ${this.trans['addon.review-page.header.days-ago']}`;
    }
  }

  private isReviewPage(): boolean{
    const dataSection = this.dom.querySelector('body').getAttribute('data-section');
    return dataSection && dataSection === "extension";
  }

  private msToHours(ms: number): number{
    return Math.floor((ms / (1000 * 60 * 60)) % 24)
  }

  private hoursToDays(hours: number): number{
    return Math.floor(hours/24);
  }

  public async addElement(): Promise<void>{
    if(this.isReviewPage()){
      this.numberToCurrency = new NumberToCurrency(
        this.locale
      );
      this.setLastApplicationTime();

      const discount: string = await this.numberToCurrency.convert(this.data.discount);
      const totalPrice: string = await this.numberToCurrency.convert(this.data.totalPrice);
      const totalDiscount: string = await this.numberToCurrency.convert(this.data.totalPrice - this.data.discount);

      this.dom.querySelector('.extension-review__discount').textContent = discount;
      this.dom.querySelector('.extension-review__original-price--amount').textContent = totalPrice;
      this.dom.querySelector('.extension-review__new-price--amount').textContent = totalDiscount;
      this.dom.querySelector('.extension-review__shop').textContent = this.data.shopName;
    }
  }
}
