















import Component from 'vue-class-component';
import { Prop } from "vue-property-decorator";
import { mapGetters } from "vuex";

import CaaModal from "./caa-modal/CaaModal.vue";
import CouponApplier from "./../../caa/CouponApplier";
import Offer from "../../sections/structures/Offer";
import GaHelper from "../../service/GaHelper";
import AbstractOfferSection from "./../AbstractOfferSection.vue";
import {gaParameters, redirectType} from "../../../modules/constants";

@Component({
  computed: {
    ...mapGetters({
      getOffers: 'offers/getOffers'
    })
  },
  components: {
    CaaModal
  }
})

export default class CaaStart extends AbstractOfferSection {
  @Prop(String) shopUrlName!: string;
  @Prop(CouponApplier) couponApplier!: CouponApplier;
  @Prop(Boolean) isSubsidExist!: boolean;

  locale: string = this.$store.getters[('locale/getLocale')];
  offers: Array<Offer> = this.$store.getters[('offers/getOffers')];
  numberOfCoupons = this.couponApplier.coupons.length;
  foundCouponsHeader = this.trans('addon.caa.popup.picodi-header-x-codes-found').replace('{coupon_count}', this.numberOfCoupons);
  foundCouponsDesc = this.trans('addon.caa.popup.picodi-found-x-codes').replace('{coupon_count}', this.numberOfCoupons);
  offerVersionRedirect = this.couponApplier.getFirstOfferVersionRedirect(this.offers);

  isCashbackRedirect(): boolean {
    return this.isSubsidExist && this.getFirstOfferShopCashbackEnabled();
  }

  getFirstOfferId(): number{
    return this.offers[0].id;
  }

  getFirstOfferShopCashbackEnabled(): boolean{
    return this.offers[0].shopCashbackEnabled;
  }

  onTestButtonClick() {
    this.$emit('toggle-popup');
    this.$emit('set-modal-type', 'PROGRESS');
    this.couponApplier.caaStart();
    this.couponApplier.caaAnalyticsHelper.sendAnalytics('START');
    this.couponApplier.picometricHelper.pushEvent(this.locale, 'CAA_START', JSON.stringify({ shopUrlName: this.shopUrlName, numberOfCoupons: this.numberOfCoupons }), window.location.href);
    this.sendGaEvent();
    this.createRedirectPinTab();
  }

  async createRedirectPinTab(): Promise<void> {
    const offerMetricUrl: string = await this.generateOfferMetricUrl({
      offerId: this.getFirstOfferId(),
      locale: this.locale,
      versionRedirect: this.offerVersionRedirect,
      isCashbackOfferRedirect: this.isCashbackRedirect(),
      redirectType: redirectType.CAA,
      shopUrlName: this.shopUrlName
    });
    this.createPinTab(offerMetricUrl);
  }

  sendGaEvent() {
    GaHelper.pushEvent('start', {
      [gaParameters.PAGE_TITLE]: 'caa_home',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/caa/${this.shopUrlName}/home`,
      [gaParameters.SHOP_URL_NAME]: this.shopUrlName
    });
  }

  sendGaPageView() {
    GaHelper.pushPageView({
      [gaParameters.PAGE_TITLE]: 'caa_home',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/caa/${this.shopUrlName}/home`,
      [gaParameters.SHOP_URL_NAME]: this.shopUrlName
    });
  }

  mounted(){
    this.couponApplier.caaAnalyticsHelper.sendAnalytics('SHOW');
    this.couponApplier.picometricHelper.pushEvent(this.locale, 'CAA_SHOW', JSON.stringify({ shopUrlName: this.shopUrlName, numberOfCoupons: this.numberOfCoupons }), window.location.href);
    this.sendGaPageView();
  }
}
