



























































































































import Component from "vue-class-component";
import {Prop} from "vue-property-decorator";
import {mapGetters, mapActions} from "vuex";
import isEmpty from "lodash/isEmpty";
import isObject from "lodash/isObject";

import AbstractOfferSection from "./AbstractOfferSection.vue";
import MessageRequest from "../../modules/chrome/message/structures/MessageRequest";
import HttpResponse from "../../modules/api/structures/HttpResponseContent";
import Offer from "../sections/structures/Offer";
import ShopStructure from "../sections/structures/Shop";
import Start from "./Start.vue";
import Search from "./Search.vue";
import Shop from "./shop/Shop.vue";
import Settings from "./Settings.vue";
import Account from "./account/Account.vue";
import CaaStart from "./caa-views/CaaStart.vue";
import CashbackPrompt from "./shop/CashbackPrompt.vue";
import CashbackActivated from "./shop/CashbackActivated.vue";
import NoCashbackPrompt from "./shop/NoCashbackPrompt.vue";
import NoticePrompt from "./shop/NoticePrompt.vue";
import DropDownMenu from "./element/DropDownMenu.vue";
import CouponApplier from "./../caa/CouponApplier";
import ShopsSuggestions from "../sections/structures/ShopsSuggestions";
import CustomPromptComponent from "./custom-prompt/CustomPrompt.vue";
import CustomPrompt from "./../service/CustomPrompt";
import GaHelper from "../service/GaHelper";
import Message from "../../modules/chrome/message/MessageContent";
import browser from "../../modules/chrome/nameSpace";
import {gaParameters} from "../../modules/constants";
import Countdown from "./element/Countdown.vue";
import ErrorTracker from "../../modules/ErrorTracker";

@Component({
  computed: {
    ...mapGetters({
      getOffers: 'offers/getOffers',
      getActiveView: 'views/getActive',
      getDefaultView: 'views/getDefault',
      getShop: 'shop/getShop',
      getLocale: 'locale/getLocale',
      getCustomPrompt: 'customPrompt/getCustomPrompt'
    })
  },
  methods: {
    ...mapActions({
      setActiveView: 'views/setActiveView',
      setDefaultView: 'views/setDefaultView'
    })
  },
  components: {
    Countdown,
    CustomPromptComponent,
    Start,
    Shop,
    Search,
    Settings,
    Account,
    CaaStart,
    CashbackPrompt,
    CashbackActivated,
    NoCashbackPrompt,
    NoticePrompt,
    DropDownMenu
  }
})
export default class Popup extends AbstractOfferSection {
  @Prop(String) shopName!: string;
  @Prop(String) shopUrlName!: string;
  @Prop(String) searchShopName!: string;
  @Prop(String) loggedUserDataEmail !: string;
  @Prop(Array) searchOffers!: Array<Offer>;
  @Prop(Array) shopsSuggestions !: Array<ShopsSuggestions>;
  @Prop(Boolean) isSubsidExist !: boolean;
  @Prop(Boolean) isRecognizeShop !: boolean;
  @Prop(Boolean) isFpoEnabled !: boolean;
  @Prop(Number) firstPurchaseBonusExpireAt !: number;
  @Prop(CouponApplier) couponApplier!: CouponApplier;
  @Prop(CustomPrompt) customPrompt!: CustomPrompt;

  getActiveView!: string;
  getDefaultView!: string;
  getLocale!: string;
  getShop!: ShopStructure;
  setActiveView!: (msg) => any;

  activeViewTitles: object = {
    OFFERS: this.getShopName(),
    SETTINGS: this.trans('addon.modal.menu.settings'),
    SEARCH: this.trans('addon.modal.menu.search')
  };

  prevView: string = this.getDefaultView;
  isVisibleDropDownMenu: boolean = false;

  getShopName() {
    const shop: ShopStructure = this.$store.getters[('shop/getShop')];
    return isObject(shop) ? shop.shopName : shop;
  }

  closePopup() {
    if(this.getActiveView === 'CART') {
      this.$emit('save-caa-closure-information');
      this.couponApplier.caaAnalyticsHelper.sendAnalytics('CLOSE');
    }

    this.$emit('toggle-popup');
    this.sendGaEvent();
    this.$emit('send-close-popup-ga-event');
  }

  sendGaEvent(): void {
    const shopUrlName = this.shopUrlName ? this.shopUrlName : 'n/a';
    GaHelper.pushEvent('close', {
      [gaParameters.SHOP_URL_NAME]: `[${shopUrlName}] - ${window.location.href}`,
      [gaParameters.CLOSE_TYPE]: 'x'
    });
  }

  isEnabledCloseIcon(): boolean {
    return this.getActiveView !== 'CASHBACKACTIVATED' && this.getActiveView !== 'CASHBACKPROMPT';
  }

  isEnabledMenuIcon(): boolean {
    return this.getActiveView === 'CASHBACKPROMPT' || this.getActiveView === 'NOCASHBACKPROMPT';
  }

  openMenu(): boolean {
    return this.isVisibleDropDownMenu = !this.isVisibleDropDownMenu;
  }

  isCashbackPromptView(): boolean {
    return this.getActiveView === 'CASHBACKACTIVATED' || this.getActiveView === 'CASHBACKPROMPT' || this.getActiveView === 'NOCASHBACKPROMPT';
  }

  isPicodiLogoEnabled(): boolean{
    return (this.getActiveView === this.getDefaultView && !this.searchOffers.length)
        || this.getActiveView === 'ACCOUNT'
        || this.getActiveView === 'START'
        || this.getActiveView === 'CART'
        || this.getActiveView === 'CASHBACKACTIVATED'
        || this.getActiveView === 'CASHBACKPROMPT'
        || this.getActiveView === 'NOTICEPROMPT'
  }

  changeView(view: 'OFFERS' | 'SETTINGS' | 'SEARCH' | 'ACCOUNT', shopUrlName?: string) :void {
    if(this.searchOffers && this.searchShopName) {
      this.$emit('set-search-offers', []);
      this.$emit('set-search-shop-name', '');
    }
    if(this.getActiveView === 'SEARCH' && view === 'OFFERS') {
      this.prevView = this.getActiveView;
    } else {
      this.prevView = this.getDefaultView;
    }
    if(view === 'OFFERS' && shopUrlName) {
      this.getShopData(shopUrlName, view);
    } else {
      this.setActiveView(view);
    }
  }

  getShopData (shopUrlName: string, view: string): void {
    Message.send(async (messageRequest: MessageRequest) => {
      messageRequest
          .setType('api.get.shop')
          .setAsync(true)
          .setValue('locale', this.getLocale)
          .setValue('shopUrlName', shopUrlName)
          .setHttpResponseCallback((httpResponse: HttpResponse) => {
            if (httpResponse.isSuccessful()) {
              const response = httpResponse.getResponse();
              this.$emit('set-search-shop-name', isObject(response.data.data.shopData) ? response.data.data.shopData.shopName : response.data.data.shop);
              if(response.data.data.offers.length > 0){
                this.setOffers(response.data.data.offers);
                this.setShop(response.data.data.shopData);
                this.setActiveView(view);
              } else {
                ErrorTracker.trackError('Shop recognized, but there are 0 active offers')
              }
            }else{
              const response = httpResponse.getResponse();
              ErrorTracker.trackError(`[${response.status}] ${response.data.error.msg}`);
              this.setActiveView('SEARCH');
            }
          });
    });
  }

  setOffers(offers: Array<Offer>) {
    const tmpOffers: Array<Offer> = []
    offers.forEach((offer: Offer) => {
      tmpOffers.push(new Offer(offer));
    });
    this.$store.commit('offers/setOffers', tmpOffers);
    this.$emit('set-search-offers', tmpOffers);
  }

  setShop(shop: ShopStructure) {
    this.$store.commit('shop/setShop', new ShopStructure(shop));
  }

setFont() :void {
  const font = document.createElement('style');
  font.textContent = '@font-face { font-family: "pico-ext-noto-sans"; ' +
      'font-weight: 400;' +
      'src: url("'
      + browser.runtime.getURL('../fonts/noto-sans-regular.woff2')
      + '"); }' +
      '@font-face { font-family: "pico-ext-noto-sans"; ' +
      'font-weight: 600;' +
      'src: url("'
      + browser.runtime.getURL('../fonts/noto-sans-bold.woff2')
      + '"); }';
  document.head.appendChild(font);
}

  isNotEmpty(object: Object): boolean{
    return !isEmpty(object);
  }

  isLoggedUser(): boolean {
    return this.isNotEmpty(this.loggedUserDataEmail);
  }

  isFPOExpiredBonus(): boolean {
    return this.isFpoEnabled && this.firstPurchaseBonusExpireAt && (this.getActiveView === 'CASHBACKPROMPT' || this.getActiveView === 'SEARCH' || this.getActiveView === 'OFFERS');
  }

  mounted(): void {
    this.setFont();
  }
}
