











import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
import Message from "../../../modules/chrome/message/MessageContent";
import MessageRequest from "../../../modules/chrome/message/structures/MessageRequest";
import GaHelper from "../../service/GaHelper";
import {gaParameters, messageName} from "../../../modules/constants";

@Component
export default class Checkbox extends Vue {
  @Prop(String) id!: string;
  @Prop(String) title!: string;
  @Prop(String) titleOn!: string;
  @Prop(String) titleOff!: string;
  @Prop(String) gaEventName!: string;
  @Prop(String) gaPageLocation!: string;
  @Prop(String) gaSettingName: string;
  @Prop(String) isCheckedAllCheckbox!: string;
  @Prop(Boolean) disabled!: boolean;
  @Prop(Boolean) checkedAll!: boolean;
  @Prop(Function) toggleDisabledAll!: any;
  @Prop(Function) setDefaultDisabledAll!: any;
  @Prop(Object) config!: Object;

  checked: string;
  chkbxTitle: string = this.title;
  locale: string = this.$store.getters[('locale/getLocale')];

  isChecked(id: string = ''){
    return (id != '' ? id : this.id) in this.config ? '0' : '1';
  }

  setTitle(): void {
    if(this.titleOn && this.titleOff){
      this.chkbxTitle = this.checked === '0' ? this.titleOff : this.titleOn;
    }
  }

  onChangeCheckbox(): void {
    this.setTitle();
    this.setStorageAddonConfig(this.id, this.checked);
    GaHelper.pushEvent(`${this.gaEventName}_${this.checked==='1' ? 'on' : 'off'}`, {
      [gaParameters.PAGE_TITLE]: `settings_${this.gaPageLocation}`,
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/settings/${this.gaPageLocation}`,
      [gaParameters.SETTING_NAME]: this.gaSettingName
    });

    if(this.isCheckedAllCheckbox){
      this.toggleDisabledAll();
      this.removeStorageAddonConfig();
    }
  }


  removeStorageAddonConfig(): void {
    Message.send((messageRequest: MessageRequest) => {
      messageRequest
        .setType(messageName.REMOVE_CONFIG_KEY)
        .setValue('key', this.isCheckedAllCheckbox)
    });
  }

  setStorageAddonConfig(name: string, value: string): void {
    Message.send((messageRequest: MessageRequest) => {
      messageRequest
        .setType(messageName.SET_CONFIG_KEY)
        .setValue('configName', name)
        .setValue('configValue', value);
    });
  }

  getStorageAddonConfig(): void {
    this.checked = this.isChecked();
    if(!this.chkbxTitle) {
      this.setTitle();
    }
    if(this.isCheckedAllCheckbox) {
      this.setDefaultDisabledAll();
    }
  }

  created() : void {
    this.getStorageAddonConfig();
  }
}
