import StorageSync from "./chrome/storage/StorageSync";
import {storageSyncKey, testVariant} from "./constants";
import {isEmpty} from "lodash";
import {isEven} from "./functions";
import ApiConfig from "../content/service/ApiConfig";
import ApiDataManager from "../content/service/ApiDataManager";
import ErrorTracker from "./ErrorTracker";

const DEFAULT_TEST_VARIANT = testVariant.A;
export default class TestVariantManager {
  private locale: string;
  constructor(locale: string) {
    this.locale = locale;
  }

  async get(): Promise<testVariant> {
    return await this.getFromStorage() || await this.fetch() || DEFAULT_TEST_VARIANT;
  }

  async fetch(): Promise<testVariant | false> {
    try {
      const userProperties = new ApiDataManager('api.get.user.properties', {locale: this.locale })
      const userPropertiesData = await userProperties.getApiData();

      this.saveToStorage(userPropertiesData.installationDate, userPropertiesData.group);
      return userPropertiesData.group;
    } catch(error) {
      ErrorTracker.trackError(error);
      return false;
    }
  }
  set(installationDate: string): testVariant {
    const parseDate = Date.parse(installationDate);
    const installationHour = new Date(parseDate).getHours();
    return isEven(installationHour) ? testVariant.A : testVariant.B;
  }

  async getProps() {
    const { config } = await ApiConfig.get(this.locale);
    const testAssignment = await this.get();
    return {
      isAssignedToTheTestA: config['AddOn.Chrome.ABTestsEnabled'] === '0' || testAssignment === testVariant.A,
      isAssignedToTheTestB: config['AddOn.Chrome.ABTestsEnabled'] === '1' && testAssignment === testVariant.B
    }
  }

  async saveToStorage(installationDate: string, testVariant?: testVariant): Promise<void> {
    const userProperties = await this.getFromStorage();
    if(userProperties) return;

    StorageSync.set({
      [storageSyncKey.USER_PROPERTIES]:
        {
          testVariant: testVariant ?? this.set(installationDate),
          installationDate: installationDate
        }
    });

  }

  async getFromStorage(): Promise<testVariant | false> {
    const userProperties = await StorageSync.get(storageSyncKey.USER_PROPERTIES);
    return !isEmpty(userProperties) ? userProperties.testVariant : false;
  }
}