import caaPackage from "@mobile-rewards/caa-module/package.json";
import Message from "../../modules/chrome/message/MessageContent";
import MessageRequest from "../../modules/chrome/message/structures/MessageRequest";
import CouponApplier from "../caa/CouponApplier";
import {messageName} from "../../modules/constants";
import extensionVersion from "../../modules/chrome/version";
import browser from "../../modules/chrome/nameSpace";

export default class caaAnalyticsHelper {
  private caa: CouponApplier;
  private store: object;
  private readonly locale: string;

  constructor(caa: CouponApplier, store: object, locale: string) {
    this.caa = caa;
    this.store = store;
    this.locale = locale;
  }

  public sendAnalytics(category: string): void {
    const caaAnalytics = this.createCaaAnalyticsObject(category);

    Message.send((messageRequest: MessageRequest) => {
      messageRequest
        .setType(messageName.BTB_SEND_CAA_ANALYTICS)
        .setValue('data', { caaAnalytics })
        .setValue('locale', this.locale);
    });
  }

  private createCaaAnalyticsObject(category: string) {
    return {
      category: category,
      type: 'caa',
      url: window.location.href,
      caa: {
        version: caaPackage.version,
        coupon: category === 'SUCCESS' ? this.caa.bestCoupon.code : undefined,
      },
      price: {
        saved: category === 'SUCCESS' ? Number.parseFloat(this.caa.bestCoupon.discount.toFixed(2)) : undefined,
        total: this.caa.total,
      },
      button: {
        id: browser.runtime.id,
        version: extensionVersion.get(),
      },
      store: {
        name: this.store.name,
        id: this.store.id,
      },
      config: {
        id: this.caa.config.id,
        type: 'caa',
      },
    };
  }
}


