class shopConfig {
  private readonly showCashbackPrompt: boolean;
  private readonly showCaa: boolean;
  private readonly showNoCashbackPrompt: boolean;
  private readonly enableInAddOn: boolean;

  constructor(data: any) {
    this.showCashbackPrompt = data.showCashbackPrompt;
    this.showCaa = data.showCaa;
    this.showNoCashbackPrompt = data.showNoCashbackPrompt;
    this.enableInAddOn = data.enableInAddOn;
  }

  public isShowCashbackPrompt(): boolean{
    return !!this.showCashbackPrompt;
  }

  public isShowCaa(): boolean{
    return !!this.showCaa;
  }

  public isShowNoCashbackPrompt(): boolean{
    return !!this.showNoCashbackPrompt;
  }

  public isEnableInAddOn(): boolean{
    return !!this.enableInAddOn;
  }
}

export default shopConfig;