import ApiDataManager from "./ApiDataManager";
import DomainRecognize from "./DomainRecognize";
import {CustomPromptDetails} from "../sections/structures/CustomPromptDetails";
import StorageSync from "../../modules/chrome/storage/StorageSync";
import {storageSyncKey} from "../../modules/constants";
import isEmpty from "lodash/isEmpty";

const HIDING_TIME_CUSTOM_PROMPT = 3600000; // 1 hour

export default class CustomPrompt {
  private locale: string;
  private customPromptId: number;
  private domainRecognize: DomainRecognize;
  private urlWithoutParameters: string;
  private customPromptWasVisible: {[key: string]: number};

  public constructor(locale: string, domainRecognize: DomainRecognize) {
    this.locale = locale;
    this.domainRecognize = domainRecognize;
    this.urlWithoutParameters = this.domainRecognize.getUrlWithoutParameters();
    this.setCustomPromptWasVisible();
  }

  public async isTriggeringUrl() {
    const triggerUrlList = await this.getList();

    if(this.urlWithoutParameters in triggerUrlList){
      this.customPromptId = triggerUrlList[this.urlWithoutParameters];
    }else{
      this.customPromptId = null;
    }

    return this.urlWithoutParameters in triggerUrlList && this.isActive();
  }

  public async getDetails(): Promise<object | false | void> {
    const customPromptDetails = new ApiDataManager('api.get.custom.prompt.details', {locale: this.locale, customPromptId: this.customPromptId}, 0);
    return await customPromptDetails.getData(['customPromptId', this.customPromptId]);
  }

  public save() {
    this.customPromptWasVisible[this.customPromptId] = Date.now();
    this.setStorage(this.customPromptWasVisible);
    return true;
  }

  public getCustomPromptId() {
    return this.customPromptId;
  }

  public getTriggerUrl(){
    return this.urlWithoutParameters;
  }

  private async getList(){
    const customPromptList = new ApiDataManager('api.get.custom.prompt.list', {locale: this.locale}, 1/4, 1/2);
    return await customPromptList.getData();
  }

  private isActive(){
    if(this.customPromptWasVisible[this.customPromptId]) {
      return (Date.now() - this.customPromptWasVisible[this.customPromptId]) > HIDING_TIME_CUSTOM_PROMPT;
    }else{
      return true;
    }
  }

  private removeExpired(){
    if(!isEmpty(this.customPromptWasVisible)) {
      for(const key in this.customPromptWasVisible){
        if((Date.now() - this.customPromptWasVisible[key]) > HIDING_TIME_CUSTOM_PROMPT){
          delete this.customPromptWasVisible[key];
        }
      }
    }
  }

  private setStorage(customPromptWasVisible: {[key: string]: number}) {
    StorageSync.set({[storageSyncKey.IS_CUSTOM_PROMPT_WAS_VISIBLE]: JSON.stringify(customPromptWasVisible)})
  }

  private getStorage(): Promise<any> {
    return StorageSync.get(storageSyncKey.IS_CUSTOM_PROMPT_WAS_VISIBLE);
  }

  private setCustomPromptWasVisible() {
    this.getStorage()
      .then((customPromptWasVisible) => {
        this.customPromptWasVisible = customPromptWasVisible ? JSON.parse(customPromptWasVisible) : {};
        this.removeExpired();
      })
      .catch(() => this.customPromptWasVisible = {})
  }
}