


















































import {Prop} from "vue-property-decorator";
import Component from 'vue-class-component'
import AbstractOfferSection from "./../AbstractOfferSection.vue";
import Shop from "../../sections/structures/Shop";
import Offer from "../../sections/structures/Offer";
import cashbackActivations from "../../../modules/cashbackActivations";
import date from "../../../modules/date";

import GaHelper from "../../service/GaHelper";
import {gaParameters, redirectType} from "../../../modules/constants";

@Component
export default class Cashback extends AbstractOfferSection {
  @Prop(Boolean) isRecognizeShop!: boolean;
  @Prop(Boolean) isFpoEnabled!: boolean;
  @Prop(String) searchShopName!: string;
  shop: Shop = this.$store.getters[('shop/getShop')];
  offers: Array<Offer> = this.$store.getters[('offers/getOffers')];
  locale: string = this.$store.getters[('locale/getLocale')];
  isActivatedCashback: boolean = false;
  lastCashbackActivationTime: string = '';
  isFpoCashbackExist: boolean = false;
  isPremiumCashbackExist: boolean = false;

  setIsActivatedCashback(){
    cashbackActivations.getLastTime(this.shop, this.offers)
      .then(lastCashbackActivationTime => {
        if(lastCashbackActivationTime){
          this.lastCashbackActivationTime = date.msToDate(lastCashbackActivationTime)
          this.isActivatedCashback = true;
        } else{
          this.isActivatedCashback = false;
        }
      })
      .catch(() => {
        this.isActivatedCashback = false;
      })
  }

  showCashbackActivatedView(){
    this.$store.commit('views/setActive', 'NOTICEPROMPT');
  }

  async activeCashback(){
    const metricUrl = await this.generateOfferMetricUrl({
      shopId: this.shop.shopId,
      shopUrlName: this.shop.shopUrlName,
      locale: this.locale,
      versionRedirect: this.shop.versionRedirect,
      redirectType: redirectType.SHOP_CASHBACK,
    });
    if(this.isRecognizeShop && !this.searchShopName) {
      await this.createPinTab(metricUrl);
      this.showCashbackActivatedView();
    }else {
      this.createTab(metricUrl, true)
    }
    cashbackActivations.save(`${this.shop.shopId}s`);
    this.sendGaEvent();
  }

  async setBonusValue() {
    this.isFpoCashbackExist = !!this.shop.fpoCashbackDesc && this.isFpoEnabled;
    this.isPremiumCashbackExist = !!this.shop.premiumCashbackDesc;
  }

  sendGaPageView() {
    GaHelper.pushPageView({
      [gaParameters.PAGE_TITLE]: 'shop_cashback',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/offers/${this.shop.shopUrlName}/cb`,
      [gaParameters.SHOP_URL_NAME]: this.shop.shopUrlName
    });
  }

  sendGaEvent() {
    GaHelper.pushEvent('activate', {
      [gaParameters.PAGE_TITLE]: 'shop_cashback',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/offers/${this.shop.shopUrlName}/cb`,
      [gaParameters.SHOP_URL_NAME]: this.shop.shopUrlName
    });
  }

  created(){
    this.setIsActivatedCashback();
  }

  mounted(){
    this.setBonusValue();
    this.sendGaPageView();
  }
}
