































import Component from "vue-class-component"
import {Prop} from "vue-property-decorator";

import AbstractOfferSection from "./../AbstractOfferSection.vue";
import Shop from "../../sections/structures/Shop";
import Offer from "../../sections/structures/Offer";
import GaHelper from "../../service/GaHelper";
import StorageSync from "../../../modules/chrome/storage/StorageSync";
import cashbackActivations from "./../../../modules/cashbackActivations";
import cashbackAvailableView from "../../service/CashbackAvailableView";
import {storageSyncKey, redirectType, gaParameters} from "../../../modules/constants";

@Component
export default class CashbackPrompt extends AbstractOfferSection {
  @Prop(Boolean) isSubsidExist!: boolean;
  @Prop(Boolean) isFpoEnabled!: boolean;
  shop: Shop = this.$store.getters[('shop/getShop')];
  locale: string = this.$store.getters[('locale/getLocale')];
  offers: Array<Offer> = this.$store.getters[('offers/getOffers')];

  activateCashback(): void {
    if(this.isSubsidExist){
      this.setCashbackActivatedView();
      this.createRedirectPinTab();
    }else{
      const picodiActivateCashbackModalUrl = this.getPicodiActivateCashbackModalUrl(this.locale, this.shop.shopUrlName, this.shop.shopId, this.shop.versionRedirect);
      this.$store.commit('views/setActive', 'OFFERS');
      this.createTab(picodiActivateCashbackModalUrl, true);
    }
    cashbackActivations.save(`${this.shop.shopId}s`);
    this.sendGaEvent();
  }

  setCashbackActivatedView(): void {
    StorageSync.get(storageSyncKey.IS_CASHBACK_AVAILABLE_WAS_VISIBLE)
      .then(isCashbackAvailableWasVisible => {
        if(typeof isCashbackAvailableWasVisible === 'undefined'){
          this.$store.commit('views/setActive', 'CASHBACKACTIVATED');
          StorageSync.set({[storageSyncKey.IS_CASHBACK_AVAILABLE_WAS_VISIBLE]: true});
        }else{
          this.$store.commit('views/setActive', 'NOTICEPROMPT');
        }
      })
      .catch( () => {
        this.$store.commit('views/setActive', 'NOTICEPROMPT');
      })
  }

  setInStorageHideCashbackActivatedView(): void {
    cashbackAvailableView.hideInTimeFromConfig(this.apiConfig('AddOn.Chrome.configHideCashbackPromptInMs'), this.shop.shopId);
  }

  async createRedirectPinTab(): Promise<void> {
    const metricUrl = await this.generateOfferMetricUrl({
      shopId: this.shop.shopId,
      locale: this.locale,
      versionRedirect: this.shop.versionRedirect,
      redirectType: redirectType.CASHBACK_PROMPT,
      shopUrlName: this.shop.shopUrlName
    });
    this.createPinTab(metricUrl);
  }

  openCashbackView(): void {
    if(this.isSubsidExist){
      this.$store.commit('views/setDefault', 'OFFERS');
      this.$store.commit('views/setActive', 'OFFERS');
    }else{ //TODO - delete if task 16996 is finished
      const picodiActivateCashbackModalUrl = this.getPicodiActivateCashbackModalUrl(this.locale, this.shop.shopUrlName, this.shop.shopId, this.shop.versionRedirect);
      this.$store.commit('views/setActive', 'OFFERS');
      this.createTab(picodiActivateCashbackModalUrl, true);
    }
  }

  closePopup() {
    this.$emit('send-ga-event');
    this.$emit('toggle-popup');
  }

  sendGaEvent(): void {
    if(this.isSubsidExist) {
      GaHelper.pushEvent('activate', {
        [gaParameters.PAGE_TITLE]: 'cashback_prompt',
        [gaParameters.PAGE_LOCATION]: `/${this.locale}/cb/${this.shop.shopUrlName}/notify`,
        [gaParameters.SHOP_URL_NAME]: this.shop.shopUrlName
      });
    }else{
      GaHelper.pushEvent('redirect_shoppage_modal', {
        [gaParameters.PAGE_TITLE]: 'cashback_prompt',
        [gaParameters.PAGE_LOCATION]: `/${this.locale}/cb/${this.shop.shopUrlName}/notify`,
        [gaParameters.SHOP_URL_NAME]: this.shop.shopUrlName
      });
    }
  }

  sendGaPageView(): void {
    GaHelper.pushPageView({
      [gaParameters.PAGE_TITLE]: 'cashback_prompt',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/cb/${this.shop.shopUrlName}/notify`,
      [gaParameters.SHOP_URL_NAME]: this.shop.shopUrlName
    });
  }

  mounted() {
    this.sendGaPageView();
    this.setInStorageHideCashbackActivatedView();
  }

}
