






















































import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
import {gaParameters, myPicodiUrl} from "../../../modules/constants";
import GaHelper from "../../service/GaHelper";
import Checkbox from "./../element/Checkbox.vue";
import Select from "./../element/Select.vue";

@Component({
  components: {Checkbox, Select}
})

export default class General extends Vue {
  @Prop(Object) config!: Object;

  locale: string = this.$store.getters[('locale/getLocale')];

  getMyPicodiPersonalDataAccountUrl(): string{
    return `${myPicodiUrl}/${this.locale}/account/personal-data`;
  }

  mounted() {
    GaHelper.pushPageView({
      [gaParameters.PAGE_TITLE]: 'settings_general',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/settings/general`
    });
  }
}
