import browser from "../nameSpace";
import Cookie = browser.cookies.Cookie;
import CookieDetails = browser.cookies.SetDetails;
import {picodiUrl} from "../../constants";
import ErrorTracker from "../../ErrorTracker";

class Cookies {
  private get(cookieName: string){
    console.debug('Cookies - get: ', cookieName);
    return browser.cookies.get({'url': picodiUrl, 'name': cookieName});
  }

  public set(cookieDetails: CookieDetails){
    console.debug('Cookies - set: ', cookieDetails.name);
    browser.cookies.set(cookieDetails);
  }

  public remove(cookieDetails: CookieDetails){
    console.debug('Cookies - remove: ', cookieDetails.name);
    browser.cookies.remove(cookieDetails);
  }

  public getAll(): Promise<Array<Cookie>>{
    console.debug('Cookies - getAll');
    return browser.cookies.getAll({'url': picodiUrl});
  }

  public async getValue(cookieName: string){
    try {
      const cookie = await this.get(cookieName);
      if(cookie && typeof cookie !== 'undefined' && cookie.value){
        return cookie.value;
      }
    } catch (e) {
      ErrorTracker.trackError(`The cookie ${cookieName} does not exist.`);
    }
  }
}

export default new Cookies();