import Message from "../../modules/chrome/message/MessageContent";
import MessageRequest from "../../modules/chrome/message/structures/MessageRequest";
import {messageName} from "../../modules/constants";

class GaHelper {
  public pushPageView(params: object): void {
    Message.send((messageRequest: MessageRequest) => {
      messageRequest
        .setType(messageName.GA_PUSH_PAGE_VIEW)
        .setAsync(true)
        .setValue('params', params);
    });
  }
  public pushError(params: object): void {
    Message.send((messageRequest: MessageRequest) => {
      messageRequest
        .setType(messageName.GA_PUSH_ERROR)
        .setAsync(true)
        .setValue('params', params);
    });
  }
  public pushEvent(name: string, params: object): void {
    Message.send((messageRequest: MessageRequest) => {
      messageRequest
        .setType(messageName.GA_PUSH_EVENT)
        .setAsync(true)
        .setValue('name', name)
        .setValue('params', params);
    });
  }
}

export default new GaHelper();