









import AbstractOfferSection from "./AbstractOfferSection.vue";
import {Prop} from "vue-property-decorator";
import Component from "vue-class-component";
import GaHelper from "../service/GaHelper";
import {gaParameters} from "../../modules/constants";
@Component()
export default class SerpDecoration extends AbstractOfferSection {
  @Prop(Boolean) isFpoEnabled!: boolean;
  @Prop(String) locale!: string;
  @Prop(String) shopUrlName!: string;
  @Prop(String) commissionDefinition!: string;
  @Prop(String) codeNumber!: string;
  @Prop(String) domain !: string;
  @Prop(String) searchUrl !: string;
  @Prop(HTMLElement) element !: HTMLElement;

  getSerpDescription() {
    if (typeof this.codeNumber !== 'undefined' && typeof this.commissionDefinition !== 'undefined') {
      return this.trans('addon.serp.decoration.cashback.and.code')
          .replace('{cashback_value}', this.commissionDefinition)
          .replace('{code_count}', this.codeNumber);
    } else if (typeof this.codeNumber !== 'undefined') {
      return this.trans('addon.serp.decoration.code')
          .replace('{code_count}', this.codeNumber);
    } else if (typeof this.commissionDefinition !== 'undefined') {
      return this.trans('addon.serp.decoration.cashback')
          .replace('{cashback_value}', this.commissionDefinition);
    }
  }

  addSendClickEventListener() {
    this.element.addEventListener('click', () => {
      this.sendClickSerpGaEvent();
    })
  }

  clickSerpElement() {
    this.sendClickSerpGaEvent();
    window.location.href = this.searchUrl;
  }

  sendClickSerpGaEvent(): void {
    GaHelper.pushEvent('click', {
      [gaParameters.PAGE_TITLE]: 'serp_decoration',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/serp/${this.shopUrlName}`,
      [gaParameters.SHOP_URL_NAME]: this.shopUrlName
    });
  }

  mounted() {
    this.addSendClickEventListener();
    GaHelper.pushPageView({
      [gaParameters.PAGE_TITLE]: 'serp_decoration',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/serp/${this.shopUrlName}`,
      [gaParameters.SHOP_URL_NAME]: this.shopUrlName
    });
  }
}
