





























import AbstractOfferSection from "./../AbstractOfferSection.vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";

import GaHelper from "../../service/GaHelper";
import ShopsSuggestions from "../../sections/structures/ShopsSuggestions";
import Shop from "../../sections/structures/Shop";
import cashbackActivations from "../../../modules/cashbackActivations";
import cashbackAvailableView from "../../service/CashbackAvailableView";
import {gaParameters, redirectType} from "../../../modules/constants";

@Component
export default class NoCashbackPrompt extends AbstractOfferSection {
  @Prop( Array ) shopsSuggestions !: Array<ShopsSuggestions>;
  @Prop(Boolean) isSubsidExist!: boolean;
  locale: string = this.$store.getters[('locale/getLocale')];
  shop: Shop = this.$store.getters[('shop/getShop')];

  async activeCashback(suggestedShop: ShopsSuggestions){
    let redirectUrl;
    if(this.isSubsidExist) {
      redirectUrl = await this.generateOfferMetricUrl({
        shopId: suggestedShop.id,
        locale: this.locale,
        versionRedirect: suggestedShop.versionRedirect,
        redirectType: redirectType.NO_CASHBACK_PROMPT,
        shopUrlName: this.shop.shopUrlName,
        destinationShopUrlName: suggestedShop.urlName
      });
      cashbackActivations.save(`${suggestedShop.id}s`);
      this.sendClickShopGaEvent(suggestedShop);
    }else{
      redirectUrl = this.getPicodiActivateCashbackModalUrl(this.locale, suggestedShop.urlName, suggestedShop.id, suggestedShop.versionRedirect);
      this.sendClickByNoRecognizeUserGaEvent(suggestedShop);
    }
    this.createTab(redirectUrl, true);
  }

  setInStorageHideCashbackActivatedView(): void{
    cashbackAvailableView.hideInTimeFromConfig(this.apiConfig('AddOn.Chrome.configHideCashbackPromptInMs'), this.shop.shopId);
  }

  sendClickShopGaEvent(shop: ShopsSuggestions): void{
    GaHelper.pushEvent('activate', {
      [gaParameters.PAGE_TITLE]: 'no-cashback_prompt',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/no-cb/${shop.urlName}/notify`,
      [gaParameters.SHOP_URL_NAME]: shop.urlName
    });
  }

  sendClickByNoRecognizeUserGaEvent(shop: ShopsSuggestions): void{
    GaHelper.pushEvent('redirect_shoppage_modal', {
      [gaParameters.PAGE_TITLE]: 'no-cashback_prompt',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/no-cb/${shop.urlName}/notify`,
      [gaParameters.SHOP_URL_NAME]: shop.urlName
    });
  }

  mounted(): void{
    this.setInStorageHideCashbackActivatedView();
    GaHelper.pushPageView({
      [gaParameters.PAGE_TITLE]: 'no-cashback_prompt',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/no-cb/${this.shop.shopUrlName}/notify`,
      [gaParameters.SHOP_URL_NAME]: this.shop.shopUrlName
    });
  }
}
