import Message from "../../modules/chrome/message/MessageContent";
import MessageRequest from "../../modules/chrome/message/structures/MessageRequest";
import {messageName} from "../../modules/constants";

class PicometricHelper {
  public pushEvent(locale: string, label: string, data: string, uri_str: string): void {
    Message.send((messageRequest: MessageRequest) => {
      messageRequest
        .setType(messageName.PICOMETRIC_PUSH_EVENT)
        .setAsync(true)
        .setValue('locale', locale)
        .setValue('event', 'c')
        .setValue('label', label)
        .setValue('data', data)
        .setValue('uri_str', uri_str)

    });
  }
}

export default new PicometricHelper();