





























import AbstractOfferSection from "./../AbstractOfferSection.vue";
import {CustomPromptDetails, CustomPromptDetailsShop} from "../../sections/structures/CustomPromptDetails";
import Component from "vue-class-component";
import {Prop} from "vue-property-decorator";
import CustomPrompt from "../../service/CustomPrompt";
import cashbackActivations from "../../../modules/cashbackActivations";
import GaHelper from "../../service/GaHelper";
import {redirectType, gaParameters} from "../../../modules/constants";

@Component({})
export default class CustomPromptShop extends AbstractOfferSection {
  @Prop(Boolean) isSubsidExist!: boolean;
  @Prop(Boolean) isFpoEnabled!: boolean;
  @Prop(CustomPrompt) customPrompt!: CustomPrompt;
  locale: string = this.$store.getters[('locale/getLocale')];
  customPromptDetails: CustomPromptDetails = this.$store.getters[('customPrompt/getCustomPrompt')];

  async getMetricRedirectUrl(customPromptDetailsShop: CustomPromptDetailsShop){
    return this.generateOfferMetricUrl({
      locale: this.locale,
      versionRedirect: this.customPromptDetails.redirect_version,
      customPromptId: this.customPrompt.getCustomPromptId(),
      shopNumber: customPromptDetailsShop.shopNumber,
      redirectType: redirectType.CUSTOM_PROMPT_SHOP,
    });
  }

  getPicodiModalUrl(customPromptDetailsShop: CustomPromptDetailsShop){
    return this.getPicodiActivateCashbackModalUrl(
        this.locale,
        customPromptDetailsShop.shopUrlName,
        customPromptDetailsShop.shopId,
        customPromptDetailsShop.shopVersionRedirect
    );
  }

  sendGaEvent(shopUrlName: string): void{
    GaHelper.pushEvent(`redirect`, {
      [gaParameters.PAGE_TITLE]: `custom_prompt`,
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/custom-prompt`,
      [gaParameters.SHOP_URL_NAME]: shopUrlName
    });
  }
  async onClickCustomPrompt(customPromptDetailsShop: CustomPromptDetailsShop){
    cashbackActivations.save(`${customPromptDetailsShop.shopId}s`);
    this.createTab(
        this.isSubsidExist ? await this.getMetricRedirectUrl(customPromptDetailsShop) : this.getPicodiModalUrl(customPromptDetailsShop),
        true
    );
    this.sendGaEvent(customPromptDetailsShop.shopUrlName);
  }
}
