















import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
import Shop from "../../../sections/structures/Shop";

@Component
export default class CaaModalFailed extends Vue {
  @Prop(String) shopDescription!: string;
  shop: Shop = this.$store.getters[('shop/getShop')];
  locale: string = this.$store.getters[('locale/getLocale')];

  getCashbackTitle(): string {
    return `${this.trans('addon.caa.modal.you-will-get')} ${this.shopDescription}`;
  }

  buttonContinueClickHandler() {
    this.$emit('continue-button-click', 'no_success');
  }
}
