import urlParser from "./urlParser";
import StorageSync from "../chrome/storage/StorageSync";
import Offer from "../../content/sections/structures/Offer";
import Shop from "../../content/sections/structures/Shop";
import isEmpty from "lodash/isEmpty";

const CASHBACK_INFO = 180000; // 3 minutes

class CashbackActivations {
  shopIds: {[key: string]: number} = {};
  offerIds: {[key: string]: number} = {};
  cashbackActivationsOfferId: number;
  parsedLastActivations: {[key: string]: number};

  constructor(){
    this.setParsedLastActivations();
  }

  public save(id: string) {
    if(id) {
      this.parsedLastActivations[id] = Date.now();
      this.setStorage(this.parsedLastActivations);
    }
  }

  public saveFromRedirect(url: string, locale: string) {
    const id = urlParser.getShopId(url, locale);
    this.save(id)
  }

  public async getLastTime(shop: Shop, offers: Array<Offer>): Promise<number> {
    const isActiveCashbackInShop = await this.isActiveCashback(shop, offers);
   if(this.cashbackActivationsOfferId){
      return this.parsedLastActivations[`${this.cashbackActivationsOfferId}o`];
    }else if(isActiveCashbackInShop){
      return this.parsedLastActivations[`${shop.shopId}s`];
    }
  }

  private removeExpired(){
    if(!isEmpty(this.parsedLastActivations)) {
      for(const key in this.parsedLastActivations){
        if((Date.now() - this.parsedLastActivations[key]) > CASHBACK_INFO){
          delete this.parsedLastActivations[key];
        }
      }
    }
  }

  private setStorage(parsedLastActivations: {[key: string]: number}) {
    this.removeExpired();
    StorageSync.set({'lastCashbackActivations': JSON.stringify(parsedLastActivations)})
  }

  private setParsedLastActivations(){
    this.getStorage()
      .then((lastCashbackActivations) => {
        this.parsedLastActivations = lastCashbackActivations ? JSON.parse(lastCashbackActivations) : {};
        this.removeExpired();
      })
      .catch(() => this.parsedLastActivations = {})
  }

  private getStorage(): Promise<any> {
    return StorageSync.get('lastCashbackActivations');
  }

  private getShopIds(parsedLastCashbackActivations: {[key: string]: number}) {
    return Object.fromEntries(Object.entries(parsedLastCashbackActivations).filter(([key]) => key.slice(-1) === 's') );
  }

  private getOfferIds(parsedLastCashbackActivations: {[key: string]: number}) {
    return Object.fromEntries(Object.entries(parsedLastCashbackActivations).filter(([key]) => key.slice(-1) === 'o') );
  }

  // check if cashback is activated by the shop
  private isShopActvie(shopId: number) {
    this.shopIds = this.getShopIds(this.parsedLastActivations);
    if(!isEmpty(this.shopIds) && this.shopIds.hasOwnProperty(`${shopId}s`)) {
      return Date.now() - this.shopIds[`${shopId}s`] < CASHBACK_INFO;
    }else {
      return false;
    }
  }

  // check cashback activated by offer
  private isOfferActive(offers: Array<Offer>, cashbackOfferId: number) {
    this.offerIds = this.getOfferIds(this.parsedLastActivations);
    if(isEmpty(this.offerIds)) return false;
    this.cashbackActivationsOfferId = offers.map(el => el.id).find(offerId => this.offerIds.hasOwnProperty(`${offerId}o`));

    if(!this.cashbackActivationsOfferId && this.offerIds.hasOwnProperty(`${cashbackOfferId}o`)){
      this.cashbackActivationsOfferId = cashbackOfferId;
    }

    if(this.cashbackActivationsOfferId){
      return Date.now() - this.offerIds[`${this.cashbackActivationsOfferId}o`] < CASHBACK_INFO
    } else {
      return false;
    }
  }

  public async isActiveCashback(shop: Shop, offers: Array<Offer>): Promise<boolean> {
    if(isEmpty(this.parsedLastActivations)) return false;

    if(this.isShopActvie(shop.shopId)) {
      return true;
    }else{
      return this.isOfferActive(offers, shop.cashbackOfferId);
    }
  }

}

export default new CashbackActivations()

