

































import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";

import NumberToCurrency from "./../../../../modules/NumberToCurrency";
import Shop from "../../../sections/structures/Shop";

@Component
export default class CaaModalSuccess extends Vue {
  @Prop(Object) bestCoupon!: object;
  @Prop(Number) totalPriceInPage!: number;
  @Prop(String) shopDescription!: string;

  shop: Shop = this.$store.getters[('shop/getShop')];
  locale: string = this.$store.getters[('locale/getLocale')];
  numberToCurrency = new NumberToCurrency(this.locale);
  totalPriceWithCurrency: string = '';
  totalPriceWithDiscountAndCurrency: string = '';

  getCashbackTitle(): string {
    return `${this.trans('addon.caa.modal.you-will-also-get')} ${this.shopDescription}`;
  }

  buttonContinueClickHandler(): void {
    this.$emit('continue-button-click', 'success')
  }

  async created(): Promise<void> {
    this.totalPriceWithCurrency = await this.numberToCurrency.convert(this.totalPriceInPage);
    this.totalPriceWithDiscountAndCurrency = await this.numberToCurrency.convert(this.totalPriceInPage - this.bestCoupon.discount );
  }
}
