// @ts-ignore
import CouponApplierProcessor from "@mobile-rewards/caa-module";
import ApiDataManager from "../service/ApiDataManager";
import Offer from "../sections/structures/Offer";
import StorageSync from "../../modules/chrome/storage/StorageSync";
import CaaAnalyticsHelper from "../service/CaaAnalyticsHelper";
import PicometricHelper from "../service/PicometricHelper";
import {storageSyncKey} from "../../modules/constants";
import isEmpty from "lodash/isEmpty";
import ErrorTracker from "../../modules/ErrorTracker";

class CouponApplier {
  public locale: string;
  public shopName: string;
  public caaAnalyticsHelper: CaaAnalyticsHelper;
  public picometricHelper: typeof PicometricHelper;
  public caa: CouponApplierProcessor;
  public coupons: Array<object>;
  public progress: number;
  public currentCoupon: string;
  public status: string;
  public result: string;
  public tabId: number;

  constructor(locale: string, shopName: string) {
    this.locale = locale;
    this.shopName = shopName;
    this.currentCoupon = '';
    this.progress = 0;
  }

  public async initialize(offers: Array<string>, tabId: number){
    this.coupons = this.getCoupons(offers);
    const configCaaFromBtb = await this.fetchConfiguration();
    const caaIsAvailable = Array.isArray(configCaaFromBtb) ? this.isCaaAvailable(configCaaFromBtb) : false;

    if(!caaIsAvailable || this.coupons.length === 0){
      return false
    }else{
      // window.isCaainitialize - initialisation of a caa once
      if(!window.isCaainitialize) {
        window.isCaainitialize = true;
        this.tabId = tabId;
        this.removeCaaStatusFromStorage();
        this.caa = new CouponApplierProcessor({
          'configs': configCaaFromBtb,
          'coupons': this.coupons,
          'id': 'picodi',
        });
        this.caaAnalyticsHelper = new CaaAnalyticsHelper(this.caa, {'id':this.caa.config.storeID, 'name': this.shopName}, this.locale);
        this.picometricHelper = PicometricHelper;
      }
      return true;
    }
  }

  private getCoupons(offers: Array<string>): Array<{code: string}>{
    return offers.map((code) => ({ code: code }));
  }

  public getFirstOfferVersionRedirect(offers: Array<Offer>): number{
    return offers[0].versionRedirect;
  }

  public caaInit(){
    this.caa.init()
  }

  public caaStart(){
    if(this.caa) {
      this.caa.process()
    }
  }

  public async caaStop() {
    if(this.caa) {
      await this.caa.stop();
    }
  }

  public async caaReset() {
    await this.caa.reset(false);
    await this.caa.reload();
  }

  public saveCaaStatusInStorage(caaStorageProgress: object){
    this.getCaaStatusFromStorage()
      .then((caaStorageProgressObject) => {
        if(typeof caaStorageProgressObject !== 'undefined'){
          const assignObject = Object.assign({}, caaStorageProgressObject, {[this.tabId]: caaStorageProgress});
          StorageSync.set({[storageSyncKey.CAA]: assignObject});
        }else{
          StorageSync.set({[storageSyncKey.CAA]: {[this.tabId]: caaStorageProgress}});
        }
      });
  }

  public removeCaaStatusFromStorage(): void{
    this.getCaaStatusFromStorage()
      .then((caaStorageProgressObject) => {
        if(typeof caaStorageProgressObject !== 'undefined' && caaStorageProgressObject){
          delete caaStorageProgressObject[this.tabId];
          StorageSync.set({[storageSyncKey.CAA]: caaStorageProgressObject});
        }
      });
  }

  public async getTabCaaStatusFromStorage(): Promise<string>{
    const caaStatusFromStorage = await this.getCaaStatusFromStorage();

    if(isEmpty(caaStatusFromStorage) || isEmpty(caaStatusFromStorage[this.tabId])){
      return Promise.reject();
    }else{
      return caaStatusFromStorage[this.tabId];
    }
  }

  private isCaaAvailable(configCaaFromBtb: Array<object>): boolean{
    const currentPageUrl = window.location.href;
    for (const pageConfig of configCaaFromBtb) {
      // @ts-ignore
      let {url}: {url: string} = pageConfig;
      if (isEmpty(url)){
        continue;
      }
      if(Array.isArray(url)){
        for (const pageConfigChildUrl of url){
          const pageRegExp = new RegExp(pageConfigChildUrl);
          if (pageRegExp.test(currentPageUrl)) {
            return true;
          }
        }
      }else{
        const pageRegExp = new RegExp(url);
        if (pageRegExp.test(currentPageUrl)) {
          return true;
        }
      }
    }
  }

  private getCaaStatusFromStorage(): Promise<{[index: number]:any} | undefined>{
    return StorageSync.get(storageSyncKey.CAA);
  }

  private async fetchConfiguration(): Promise<Array<{ url: string }> | boolean>{
    const configCaaFromBtb = new ApiDataManager('api.caa', {locale: this.locale}, 1);
    const configCaaFromBtbData = await configCaaFromBtb.getData();

    if(!isEmpty(configCaaFromBtbData)){
      return configCaaFromBtbData.caa;
    }else{
      ErrorTracker.trackError('Unable to get caa configurations'); // TODO wyswietlic info w popup
      return false;
    }
  }
}

export default CouponApplier;