




































import Vue from "vue";
import Component from "vue-class-component";
import {mapGetters} from "vuex";

import Shop from "../../sections/structures/Shop";
import {Prop} from "vue-property-decorator";

@Component({
  computed: {
    ...mapGetters({
      getActiveView: 'views/getActive'
    })
  },
})
export default class Countdown extends Vue {
  @Prop(Boolean) firstPurchaseBonusExpireAt!: number;
  shop: Shop = this.$store.getters[('shop/getShop')];
  locale: string = this.$store.getters[('locale/getLocale')];
  currentTime: number = new Date().getTime();
  intervalId: ReturnType<typeof setTimeout> | null = null;

  get timeDifference(): number {
    return this.firstPurchaseBonusExpireAt - this.currentTime;
  }

  get hasMoreThanOneDay(): boolean {
    const days = Math.floor(this.timeDifference / (1000 * 60 * 60 * 24));
    return days > 0;
  }

  get dayDigits(): string[] {
    const days = Math.floor(this.timeDifference / (1000 * 60 * 60 * 24));
    return String(days).split('');
  }

  get hourDigits(): string[] {
    const hours = Math.floor(this.timeDifference / (1000 * 60 * 60)) % 24;
    return String(hours).split('');
  }

  get minuteDigits(): string[] {
    const minutes = Math.floor(this.timeDifference / (1000 * 60)) % 60;
    return String(minutes).split('');
  }

  get secondsDigits(): string[] {
    const seconds = Math.floor(this.timeDifference / 1000) % 60;
    return String(seconds).split('');
  }

  created() {
    this.intervalId = setInterval(() => {
      this.currentTime = new Date().getTime();
    }, 1000);
  }

  beforeDestroy() {
    if (this.intervalId) {
      clearInterval(this.intervalId);
    }
  }
}
