import ApiConfig from "../content/service/ApiConfig";

class NumberToCurrency{
  private readonly locale: string;
  private readonly currency: Promise<string>;

  constructor(locale: string) {
    this.locale = locale;
    this.currency = this.getConfigCurrency();
  }

  public async convert(amount: number): Promise<string> {
    try{
      return new Intl.NumberFormat(
        this.locale,
        {
          style: 'currency',
          currency: await this.currency,
          minimumFractionDigits: 2,
        }
      ).format(amount)
    } catch(e){
      return `${amount} ${await this.currency ?? ''}`;
    }
  }

  private async getConfigCurrency(): Promise<string> {
    const apiConfig = await ApiConfig.get(this.locale);
    return apiConfig.config['country.currency'] ?? '';
  }
}

export default NumberToCurrency;