const state = {
  customPrompt: {}
};

const getters = {
  getCustomPrompt: state => {
    return state.customPrompt;
  }
};

const mutations = {
  setCustomPrompt: (state, payload) => {
    state.customPrompt = payload;
  }
};

export default {
  namespaced: true,
  state,
  getters,
  mutations
};