import User from "../backend/User";
import StorageSync from "../modules/chrome/storage/StorageSync";
import axios, {AxiosError} from "axios";
import CryptoJS from "crypto-js";
import {installation, storageSyncKey} from "../modules/constants";
import ErrorTracker from "../modules/ErrorTracker";

class Tracking {
  private urls: Array<object>;
  private expiredTime: number;
  private user: User;

  readonly timeFrameSendTracking: number;
  readonly numberOfUrls: number;

  constructor() {
    this.getStorageTracking();
    this.user = new User();

    this.timeFrameSendTracking = 3600000;
    this.numberOfUrls = 16;
  }

  public setTracking(url: string, isRecognize: boolean): any {
    if(!isRecognize && this.isTrackingExpired()){
      this.postTracking();
    }else if(isRecognize){
      this.setValue(url).then(() => {
        if(this.urls && (this.urls.length >= this.numberOfUrls || this.isTrackingExpired())){
          this.postTracking();
        }else{
          StorageSync.set({[storageSyncKey.TRACKING]: {'urls': this.urls, 'expiredTime': this.expiredTime ? this.expiredTime : new Date().getTime()}});
        }
      })
       .catch(e => {
         ErrorTracker.trackError(e);
       });
    }
  }

  private isTrackingExpired(): boolean{
    const currentTime = new Date().getTime();
    return this.expiredTime && (this.expiredTime + this.timeFrameSendTracking) < currentTime;
  }

  private getStorageTracking(): any {
    StorageSync.get(storageSyncKey.TRACKING).then(tracking => {
      if (typeof tracking !== 'undefined') {
        this.urls = tracking.urls;
        this.expiredTime = tracking.expiredTime;
      }else{
        this.urls = [];
      }
    })
    .catch((e) => {
      ErrorTracker.trackError(`Unable to get '${storageSyncKey.TRACKING}' from storage sync - ${e}`)
    });
  }

  private setValue(url: string): Promise<any> {
      return new Promise((resolve, reject) => {
        this.user.getInstallationId().then(({installationId}: installation) => {
          if(url !== ''){
            this.urls.push({"url": url, "timestamp": +new Date(), "installationId": installationId});
            resolve(true);
          }else{
            reject('Url cannot be blank');
          }
        }).catch(() => {
          reject('InstallationId not set');
        });
      });
  }

  private dataEncryption(){
    return CryptoJS.AES.encrypt(JSON.stringify({'urls': this.urls}), this.getSecretKey()).toString();
  }

  private getSecretKey(){
    return "2y1o2Oh7gKG3sJ5vcU10YRHUtMPVXVSjsghOXHL7NIww8";
  }

  private getLambdaUrl(){
    return 'https://zak7be8sse.execute-api.eu-central-1.amazonaws.com/picodiAddon/logs';
  }

  private postTracking(): any {
    if(this.urls.length != 0) {
      axios.post(this.getLambdaUrl(), this.dataEncryption())
        .then(() => {
          StorageSync.remove(storageSyncKey.TRACKING);
        })
        .catch((error: AxiosError) => {
          if (error.response) {
            ErrorTracker.trackError(error);
          }
        });
    }
  }
}

export default new Tracking();