














import AbstractOfferSection from "./../AbstractOfferSection.vue";
import Component from "vue-class-component";
import CustomPromptImage from "./CustomPromptImage.vue";
import CustomPromptShop from "./CustomPromptShop.vue";
import {CustomPromptDetails} from "../../sections/structures/CustomPromptDetails";
import {Prop} from "vue-property-decorator";
import CustomPrompt from "./../../service/CustomPrompt";
import GaHelper from "../../service/GaHelper";
import {gaParameters} from "../../../modules/constants";

@Component({
  components: {CustomPromptImage, CustomPromptShop}
})

export default class CustomPromptView extends AbstractOfferSection {
  @Prop(Boolean) isSubsidExist!: boolean;
  @Prop(Boolean) isFpoEnabled!: boolean;
  @Prop(CustomPrompt) customPrompt!: CustomPrompt;
  locale: string = this.$store.getters[('locale/getLocale')];
  customPromptDetails: CustomPromptDetails = this.$store.getters[('customPrompt/getCustomPrompt')];

  mounted(){
    GaHelper.pushPageView({
      [gaParameters.PAGE_TITLE]: 'custom_prompt',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/custom-prompt`,
      [gaParameters.SHOP_URL_NAME]: this.customPrompt.getTriggerUrl()
    });
    this.customPrompt.save();
  }
}
