import browser from "../modules/chrome/nameSpace";
import Cookies from "./chrome/cookies/Cookies";
import isEmpty from "lodash/isEmpty";
import { browserName } from "./constants";
import Cookie = browser.cookies.Cookie;
import { detect } from "detect-browser";
import ErrorTracker from "./ErrorTracker";

export function isSafari(): boolean {
  return getBrowserParameter() === browserName.ADDON_SAFARI;
}

export function isChrome(): boolean {
  return getBrowserParameter() === browserName.ADDON_CHROME;
}

export function isFirefox(): boolean {
  return getBrowserParameter() === browserName.ADDON_FIREFOX;
}

export function getBrowserParameter(): string {
  const browser = detect();
  switch (browser && browser.name) {
    case 'chrome':
      return browserName.ADDON_CHROME
    case 'safari':
      return browserName.ADDON_SAFARI
    case 'firefox':
      return browserName.ADDON_FIREFOX
    default:
      return browserName.ADDON_OTHER
  }
}

export function setUrlParams(url: string, params: object, hash: string = ``): string {
  const newUrl = new URL(url);
  for (const [key, value] of Object.entries(params)) {
    newUrl.searchParams.append(key, value);
  }
  newUrl.hash = hash;
  return newUrl.href;
}

export async function getCookiesFromRegex(pattern: string): Promise<Array<Cookie> | false>{
  try {
    const cookies = await Cookies.getAll();
    if(!cookies){
      return false;
    }
    const regex = new RegExp(pattern);
    const cookie = cookies.filter((cookie: Cookie) => {
      if (regex.test(cookie.name)) {
        return cookie;
      }
    });
    return !isEmpty(cookie) ? cookie : false;
  } catch (error) {
    ErrorTracker.trackError(`The cookies do not exist.`);
    //throw new Error(`The cookies do not exist.`);
  }
}

export function getLastCookieKey(cookies: Array<Cookie>): Cookie{
  const expiresCookies = cookies.map(({expirationDate}) => expirationDate);
  const lastCookieIndex = expiresCookies.indexOf(Math.min(...expiresCookies));
  return cookies[lastCookieIndex];
}

export function isEven(number: number): boolean {
  return number % 2 === 0;
}

export function getCurrentDateTime() {
  const date: Array<string> = new Date().toISOString().split('T');
  return date[0] + ' ' + date[1].slice(0,8);
}