import forEach from "lodash/forEach";

export default class SerpDecorationHelper {
  private readonly dom: HTMLDocument;
  private searchContainers: Array<object> = [];

  constructor(dom: HTMLDocument) {
    this.dom = dom;
  }

  public addToolboxElements(): void {
    forEach(this.searchContainers, (offerElement) => {
      const toolboxElement: HTMLElement = this.dom.createElement('div');
      offerElement.wrapper.appendChild(toolboxElement);
    });
  }

  public wrapElements(searchItems: Array<object>): void {
    forEach(searchItems, (item) => {
      const serpElement: HTMLElement = item.element;
      const wrapperElement: HTMLElement = this.dom.createElement('div');
      wrapperElement.classList.add('a-serp-container');
      wrapperElement.innerHTML = serpElement.outerHTML;
      serpElement.parentNode.insertBefore(wrapperElement, serpElement);

      const serpContainer = {
        element: wrapperElement.firstElementChild,
        wrapper: wrapperElement,
        serp: item.element
      }

      this.searchContainers.push(serpContainer);
    });
  }

  public createObjects(): Array<HTMLElement> {
    const searchItems: Array<HTMLElement> = [];
    forEach(this.getElements(), (element: HTMLElement) => {
      searchItems.push(element);
    });

    return searchItems;
  }

  private getElements(): NodeList {
    return this.dom.querySelectorAll('#search > div > div > div, #tads > div');
  }
}
