
	import Vue from "vue";
	import Component from "vue-class-component";
  import { Prop } from "vue-property-decorator";

	import ApiRouter from "../../modules/ApiRouter";
	import Message from "../../modules/chrome/message/MessageContent";
	import MessageRequest from "../../modules/chrome/message/structures/MessageRequest";
  import StorageSync from "../../modules/chrome/storage/StorageSync";
  import {picodiUrl, metricBaseUrl, storageSyncKey, redirectType, testVariant} from "../../modules/constants";
  import { getBrowserParameter, setUrlParams } from "../../modules/functions";
  import { messageName } from "../../modules/constants";
  import ErrorTracker from "../../modules/ErrorTracker";

  export interface Params {
    locale: string;
    versionRedirect: number;
    isCashbackOfferRedirect?: boolean;
    offerId?: number;
    shopId?: number,
    redirectType?: redirectType,
    shopUrlName?: string,
    destinationShopUrlName?: string
    shopNumber?: number;
    customPromptId?: number;
    assignedTest?: testVariant | false;
  }

	@Component()
	export default abstract class AbstractOfferSection extends Vue {
		@Prop(ApiRouter) apiRouter!: ApiRouter;

		createTab(url: string, active: boolean): void {
			Message.send((messageRequest: MessageRequest) => {
				messageRequest
					.setType(messageName.TAB_CREATE)
					.setValue('url', url)
          .setValue('pinned', false)
					.setValue('active', active);
			});
		}

    createPinTab(url: string): void {
      Message.send((messageRequest: MessageRequest) => {
        messageRequest
            .setType(messageName.TAB_CREATE_AFFILIATE)
            .setValue('url', url)
            .setValue('active', false)
            .setValue('pinned', true)
            .setValue('index', 0);
      });
    }

    generatePicodiUrl(offerId: number, locale: string, shopUrlName: string): string {
      const url: string = `${picodiUrl}${locale}/${shopUrlName}`;
        return setUrlParams(url, {
          utm_source: 'addon',
          utm_medium: 'addon',
          utm_campaign: 'addon'
        }, `cid=${offerId}`);
    }

    getPicodiActivateCashbackModalUrl(locale: string, shopUrlName: string, shopId: number, versionRedirect: number): string{
      return `${picodiUrl}${locale}/${shopUrlName}#activate-cashback-modal_${shopId}_${versionRedirect}`;
    }

    async generateOfferMetricUrl(params: Params): Promise<string> {
      let metricUrl;

      if (params.shopNumber) {
        metricUrl = this.getCustomPromptMetricUrl(params.customPromptId, params.locale, params.shopNumber, metricBaseUrl);
      } else if (params.shopId) {
        metricUrl = this.getCashbackShopMetricUrl(params.shopId, params.locale, metricBaseUrl);
      } else if (params.isCashbackOfferRedirect) {
        metricUrl = this.getCashbackOfferMetricUrl(params.offerId, params.locale, metricBaseUrl);
      } else {
        metricUrl = this.getMetricUrl(params.offerId, params.locale, metricBaseUrl);
      }

      const metricUrlWithVersionRedirect = `${metricUrl}${params.versionRedirect ? '/v' + params.versionRedirect : ''}`;

      return this.setParamsToMetricUrl(metricUrlWithVersionRedirect, params);
    }

    async setParamsToMetricUrl(metricUrl: string, params: Params) {
      const installationId = await this.getInstallationId();
      const trbParameter = this.getTrbParameter(params);

      return setUrlParams(metricUrl, Object.assign({}, {
            iid: installationId,
            platform: getBrowserParameter()
          },
          trbParameter && {trb: trbParameter}
      ));
    }

    getMetricUrl(offerId: number, locale: string, metricBaseUrl: string): string {
      return `${metricBaseUrl}${locale}/r/${offerId}`;
    }

    getCashbackOfferMetricUrl(offerId: number, locale: string, metricBaseUrl: string): string {
      return `${metricBaseUrl}${locale}/cr/${offerId}`;
    }

    getCashbackShopMetricUrl(shopId: number, locale: string, metricBaseUrl: string): string {
      return `${metricBaseUrl}${locale}/cr/s/${shopId}`;
    }

    getCustomPromptMetricUrl(customPromptId: number, locale: string, shopNumber: number, metricBaseUrl: string): string {
      return `${metricBaseUrl}${locale}/cr/cp/${customPromptId}/${shopNumber}`;
    }

    getTrbParameter(params: Params): string{
      switch (params.redirectType) {
        case redirectType.CASHBACK_PROMPT:
          return `/${params.locale}/addon/cashback-prompt/${params.shopUrlName}`;
        case redirectType.NO_CASHBACK_PROMPT:
          return `/${params.locale}/addon/no-cashback-prompt/${params.shopUrlName}/${params.destinationShopUrlName}`;
        case redirectType.CAA:
          return `/${params.locale}/caa/${params.shopUrlName}`;
        case redirectType.SHOP_CASHBACK:
          return `/${params.locale}/addon/shop-cashback/${params.shopUrlName}`;
        case redirectType.SHOP_COUPON_CODE:
          return `/${params.locale}/addon/shop-coupon-code/${params.shopUrlName}/${params.offerId}`;
        case redirectType.SHOP_COUPON_PROMO:
          return `/${params.locale}/addon/shop-coupon-promo/${params.shopUrlName}/${params.offerId}`;
        case redirectType.CUSTOM_PROMPT_SHOP:
          return `/${params.locale}/addon/custom-prompt-shop/${params.customPromptId}`;
        case redirectType.CUSTOM_PROMPT_IMAGE:
          return `/${params.locale}/addon/custom-prompt-image/${params.customPromptId}`;
        default:
          return '';
      }
    }

    getInstallationId(): Promise<string>{
      return new Promise((resolve) => {
        StorageSync.get(storageSyncKey.INSTALLATION).then(installation => {
          resolve(installation.installationId);
        })
        .catch((e) => {
          ErrorTracker.trackError(`Unable to get 'installationId' - ${e}`)
        })
      })
    }
	}
