import Shop from "../sections/structures/Shop";
import picodiCookies from "./picodiCookies";
import Message from "../../modules/chrome/message/MessageContent";
import MessageRequest from "../../modules/chrome/message/structures/MessageRequest";
import {messageName} from "../../modules/constants";

class Bonus{
  private readonly locale: string
  constructor(locale: string) {
    this.locale = locale;
  }

  public async isFPO(): Promise<boolean> {
    return await this.isAnonymous() || !await this.isBuyer();
  }

  private isBuyer(): Promise<boolean> {
    return new Promise((resolve) => {
      Message.send((messageRequest: MessageRequest) => {
        messageRequest
          .setType(messageName.USER_IS_BUYER)
          .setAsync(true)
          .setResponseCallback((responseCallback: any) => {
            if (!responseCallback || typeof responseCallback === 'undefined') {
              resolve(false);
            } else {
              resolve(true);
            }
          });
      });
    });
  }

  public getFPBEA(): Promise<boolean> {
    return new Promise((resolve) => {
      Message.send((messageRequest: MessageRequest) => {
        messageRequest
          .setType(messageName.GET_USER_FPBEA)
          .setAsync(true)
          .setResponseCallback((responseCallback: any) => {
            resolve(responseCallback);
          });
      });
    });
  }

  private async isAnonymous(): Promise<boolean> {
    return !await picodiCookies.isSubsidExist(this.locale);
  }
}

export default Bonus;