import StorageSync from "../modules/chrome/storage/StorageSync";
import ApiClient from "../modules/api/ApiClient";
import ApiClientManager from "../modules/api/ApiClientManager";
import TestVariantManager from "../modules/TestVariantManager"
import randomStringGenerator from "../modules/RandomStringGenerator"
import locale from "./Locale";
import {installation, storageSyncKey} from "../modules/constants";
import isEmpty from "lodash/isEmpty";
import Cookies from "../modules/chrome/cookies/Cookies";
import browser from "../modules/chrome/nameSpace";
import {getCookiesFromRegex, getCurrentDateTime, getLastCookieKey} from "../modules/functions";
import picodiCookies from "../content/service/picodiCookies";
import ErrorTracker from "../modules/ErrorTracker";

export default class User {
  private apiClientManager: ApiClientManager;

  public initializeInstallationId(apiClientManager: ApiClientManager): Promise<installation> {
    this.apiClientManager = apiClientManager;
    return new Promise((resolve) => {
      this.getInstallationId().then(installation => {
        if (typeof installation === 'undefined' || isEmpty(installation)) {
          resolve(this.setInstallationId());
        } else {
          picodiCookies.setAddonInstallationId(installation)
          resolve(installation);
        }
      })
      .catch((e) => {
        ErrorTracker.trackError(`Unable to get 'installationId' - ${e}`)
      });
    })
  }

  public getInstallationId(): Promise<installation> {
    return new Promise((resolve) => {
      StorageSync.get(storageSyncKey.INSTALLATION).then(installation => {
        resolve(installation);
      })
      .catch((e) => {
        ErrorTracker.trackError(`Unable to get 'installation' from storage sync - ${e}`)
      });
    })
  }

  private setInstallationId(): installation {
    const installation: installation = {
        'installationId': randomStringGenerator.generateId(13),
        'token': randomStringGenerator.generateId(13)
      };

    if(typeof installation !== 'undefined') {
      StorageSync.set({[storageSyncKey.INSTALLATION]: installation}, () => {
        this.sendInstallationId(installation);
        picodiCookies.setAddonInstallationId(installation);
      });
    }
    return installation;
  }

  private sendConfig(locale: string, installation: installation): void {
    const configuration = JSON.stringify({'market': locale});
    const apiClient: ApiClient = this.apiClientManager.getClient(locale);

    apiClient.saveAddonConfig(installation, configuration)
  }

  private async sendInstallationId(installation: installation): Promise<void> {
    try {
      const market = await locale.getLocale();
      const apiClient: ApiClient = this.apiClientManager.getClient(market);
      const installationDate: string = getCurrentDateTime();
      let subsid: string = await Cookies.getValue(`subsid_${market}`);

      if(!subsid){
        const subsidCookie = await getCookiesFromRegex("^subsid_[a-z]{2}$");
        const lastSubsid = subsidCookie && getLastCookieKey(subsidCookie);
        subsid = lastSubsid.value;
      }

      apiClient.addonInstall(installation, market, browser.i18n.getUILanguage(), subsid, installationDate)
        .then(() => {
          this.setTestVariant(installationDate, market);
          this.sendConfig(market, installation);
        })
    }
    catch (error) {
      ErrorTracker.trackError(error);
    }
  }

  private setTestVariant(installationDate: string, locale: string) {
    const testVariantManager = new TestVariantManager(locale);
    testVariantManager.saveToStorage(installationDate);
  }
}