const state = {
  active: '',
  default: ''
};

const getters = {
  getActive: state => {
    return state.active;
  },
  getDefault: state => {
    return state.default;
  }
};

const mutations = {
  setActive: (state, payload) => {
    state.active = payload;
  },
  setDefault: (state, payload) => {
    state.default = payload;
  }
};

const actions = {
   setActiveView: ({commit}, msg) => {
     commit('setActive', msg);
   },

  setDefaultView: ({commit}, msg) => {
    commit('setDefault', msg);
  }
};

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
};