import forEach from 'lodash/forEach';
import ParameterManager from './ParameterManager';
import routing from '../config/routing';
import {apiUrlType} from "./constants";
import ErrorTracker from "./ErrorTracker";

class ApiRouter {
  private locale: string;
  private domain: string = null;
  private domainGlobal: string = null;
  private myPicodi: string = null;
  private protocol: string;
  private parameterManager: ParameterManager;

  constructor(locale: string, parameterManager: ParameterManager) {
    this.locale = locale;
    this.parameterManager = parameterManager;
    this.protocol = parameterManager.get('api.protocol');
    this.domainGlobal = this.parameterManager.get('api.globalHost');
    this.myPicodi = this.parameterManager.get('myPicodi.host');
    this.determineDomain();
  }

  private determineDomain(): void {
    const hosts =  this.parameterManager.get('api.hosts');

    forEach(hosts, host => {
      forEach(host.locales, locale => {
        if (this.locale === locale) {
          this.domain = host.domain;
          return false;
        }
      });
      if (this.domain) {
        return false;
      }
    });
  }

  public getUrl(key: string, params: any = null, apiUrl: apiUrlType = apiUrlType.API_DEFAULT ) {
    const path: string = this.getPath(key, params);
    const apiUrlObject = {
      [apiUrlType.API_DEFAULT]: `${this.protocol}://${this.domain}/api/addon/${this.locale}${path}`,
      [apiUrlType.API_EC1_WITH_MARKET]: `${this.protocol}://${this.domainGlobal}/api/addon/${this.locale}${path}`,
      [apiUrlType.API_EC1_WITHOUT_MARKET]: `${this.protocol}://${this.domainGlobal}/api/addon${path}`,
      [apiUrlType.MY_PICODI]: `${this.protocol}://${this.myPicodi}/${this.locale}${path}`
    }

    return apiUrlObject[apiUrl];
  }

  public getPath(key: string, params: any = null): string {
    if (!routing.api.hasOwnProperty(key)) {
      ErrorTracker.trackError(`Unable to find route "${key}"!`);
      //throw new Error(`Unable to find route "${key}"!`);
    }
    return routing.api[key](params);
  }
}

export default ApiRouter;
