import Message from "../../modules/chrome/message/MessageContent";
import MessageRequest from "../../modules/chrome/message/structures/MessageRequest";
import {installation, messageName, picodiUrl, secondsSinceTheUnix, sixMonthsInSeconds} from "../../modules/constants";
import Cookies from "../../modules/chrome/cookies/Cookies";

function isSubsidExist(locale: string): Promise<boolean> {
  return new Promise((resolve) => {
    Message.send((messageRequest: MessageRequest) => {
      messageRequest
        .setType(messageName.GET_COOKIE_SUBSID)
        .setValue('locale', locale)
        .setAsync(true)
        .setResponseCallback((responseCallback: any) => {
          if (!responseCallback || typeof responseCallback === 'undefined') {
            resolve(false);
          } else {
            resolve(true);
          }
        });
    });
  });
}

function getUsAddonData(locale: string): Promise<string> {
  return new Promise((resolve) => {
    Message.send((messageRequest: MessageRequest) => {
      messageRequest
        .setType(messageName.GET_COOKIE_US_ADDON)
        .setValue('locale', locale)
        .setAsync(true)
        .setResponseCallback((responseCallback: any) => {
          if(!responseCallback || typeof responseCallback === 'undefined'){
            resolve('');
          }else{
            resolve(decodeURIComponent(responseCallback));
          }
        });
    });
  });
}

export async function setAddonInstallationId(installation: installation): Promise<void> {
  const COOKIE_INSTALLATION_ID_NAME = "addon_installation_id";
  const cookieValue = await Cookies.getValue(COOKIE_INSTALLATION_ID_NAME);

  if(cookieValue && cookieValue === installation.installationId) return;

  Cookies.set({
    "name": COOKIE_INSTALLATION_ID_NAME,
    "expirationDate": secondsSinceTheUnix + sixMonthsInSeconds,
    "domain": ".picodi.com",
    "url": picodiUrl,
    "value": installation.installationId
  })
}

export default { isSubsidExist, getUsAddonData, setAddonInstallationId }