import StorageSync from "../../modules/chrome/storage/StorageSync";
import {storageSyncKey} from "../../modules/constants";

const ONE_HOUR = 3600000;

class CashbackAvailableView {
  hideInTimeFromConfig(configHideCashbackInfoInMs: number = ONE_HOUR, shopKey: number|string): void{
    this.setInStorageIsHide(Date.now() + (configHideCashbackInfoInMs ?? ONE_HOUR), shopKey);
  }

  hideToday(shopKey: number|string): void{
    this.setInStorageIsHide(new Date().setHours(23, 59, 59, 999), shopKey);
  }

  getFromStorageIsHide(): Promise<{[index: number|string]: number}> {
    return StorageSync.get(storageSyncKey.CASHBACK_AVAILABLE_HIDE_KEY);
  }

  setInStorageIsHide(expireTime: number, shopKey: number|string): void{
    this.getFromStorageIsHide()
      .then((cashbackAvailableHide) => {
        const newCbAvailableHide = {...cashbackAvailableHide};

        newCbAvailableHide[shopKey] = expireTime;
        StorageSync.set({[storageSyncKey.CASHBACK_AVAILABLE_HIDE_KEY]: newCbAvailableHide});
      })
  }

  async isShowInShop(shopKey: number|string): Promise<boolean> {
    const cashbackAvailableHide:{[index: number|string]: number} = await this.getFromStorageIsHide();

    if(!cashbackAvailableHide || !cashbackAvailableHide[shopKey]) {
      return true;
    }
    return typeof cashbackAvailableHide[shopKey] == 'number' && cashbackAvailableHide[shopKey] < Date.now();
  }
}

export default new CashbackAvailableView();