import AbstractSection from "./abstract/AbstractSection";
import RootContent from "../RootContent";
import SerpDecorationHelper from "../service/SerpDecorationHelper";
import Serp from "../components/Serp.vue";
import ApiDataManager from "../service/ApiDataManager";
import Vue from "vue";
import store from "../store";
import forEach from "lodash/forEach";
import isEmpty from "lodash/isEmpty";
import Bonus from "../service/Bonus";

export default class SerpDecoration extends AbstractSection {
  private serpDecorationHelper: SerpDecorationHelper;
  private searchItems: Array<HTMLElement>;
  private serpDecorationDomainsObject: {[index: string]:any} = {};
  private trans: Object;
  constructor(rootContent: RootContent) {
    super(rootContent);
  }

  public render() {
    this.serpDecorationHelper = new SerpDecorationHelper(this.getDom());
    this.searchItems = this.serpDecorationHelper.createObjects();

    this.renderOfferToolboxes();
  }

  public static isEnabled(url: string): boolean{
    return new URL(url).hostname.includes('google');
  }

  private async renderOfferToolboxes(): Promise<void> {
    await this.recognizeSearchItems();
    if(!this.searchItems) return;

    this.trans = this.rootContent.getTrans();
    this.serpDecorationHelper.wrapElements(this.searchItems);
    this.serpDecorationHelper.addToolboxElements();

    Vue.mixin({
      methods: {
        // @ts-ignore
        trans: (key: string) => this.trans[key] ?? key
      }
    });

    forEach(this.searchItems, (item) => {
      new Vue({
        el: '.a-serp-container',
        store,
        render: (h: Function) => h(Serp, {
          props: {
            ...item,
            locale: this.getLocale()
          }
        })
      });
    });
  }

  private async recognizeSearchItems(){
    await this.getMapSerpDecorationDomainsObject();
    if(!this.serpDecorationDomainsObject){
      this.searchItems = [];
    }else{
      this.searchItems = this.searchItems
        .filter((item) => {
          const searchElement: HTMLAnchorElement = item.querySelector('a[href]');
          return searchElement && this.isRecognizeDomain(searchElement.href);
        }).map((item) => {
          const searchElement: HTMLAnchorElement = item.querySelector('a[href]');
          return {
            element: item,
            searchUrl: searchElement.href,
            ...this.serpDecorationDomainsObject[this.getHostUrlName(searchElement.href)]
          }
      });
    }
  }

  private getHostUrlName(url: string): string{
    try {
      return new URL(url).hostname.replace('www.', '');
    } catch (error) {
      return '';
    }
  }

  private isRecognizeDomain(url: string): boolean{
    return !isEmpty(url) ? this.getHostUrlName(url) in this.serpDecorationDomainsObject : false;
  }

  private async getMapSerpDecorationDomainsObject(){
    const serpDecorationDomains = await this.fetchSerpDecorationDomains();
    if(isEmpty(serpDecorationDomains.shops)) return;

    const bonus = new Bonus(this.getLocale());
    const isFpoEnabled = await bonus.isFPO();

    const serpDecorationDomainsShops = serpDecorationDomains.shops;
    this.serpDecorationDomainsObject = Object.assign({}, ...serpDecorationDomainsShops.map((
      {
        codeNumber,
        commissionDefinition,
        fpoCashbackDesc,
        premiumCashbackDesc,
        domain,
        shopUrlName
      }: {
        codeNumber: string;
        commissionDefinition: string;
        fpoCashbackDesc: string;
        premiumCashbackDesc: string;
        domain: string;
        shopUrlName: string
      }) => {
      if (!!fpoCashbackDesc && isFpoEnabled) {
        return { [domain]: { codeNumber, commissionDefinition: fpoCashbackDesc, shopUrlName } };
      } else if(premiumCashbackDesc) {
        return { [domain]: { codeNumber, commissionDefinition: premiumCashbackDesc, shopUrlName } };
      } else {
        return { [domain]: { codeNumber, commissionDefinition, shopUrlName } };
      }
    }));
  }

  private async fetchSerpDecorationDomains() {
    const serpDecorationDomains = new ApiDataManager('api.get.serp.decorations', {locale: this.getLocale()}, 1/4);
    return await serpDecorationDomains.getData();
  }
}