
















import Component from "vue-class-component";
import AbstractOfferSection from "./AbstractOfferSection.vue";
import Message from "../../modules/chrome/message/MessageContent";
import MessageRequest from "../../modules/chrome/message/structures/MessageRequest";
import {messageName} from "../../modules/constants";
import browser from "../../modules/chrome/nameSpace";

import General from "./settings/General.vue";
import Categories from "./settings/Categories.vue";
import Stores from "./settings/Stores.vue";

@Component({
  components: {
    'General': General,
    'Categories': Categories,
    'Stores': Stores
  }
})

export default class Settings extends AbstractOfferSection {
  locale: string = this.$store.getters[('locale/getLocale')];
  currentTab: string = 'General';
  tabs: Array<string> = ['General', 'Categories', 'Stores'];
  tabsTitle: Object = {'General': this.trans('addon.modal.settings.menu.general'), 'Categories': this.trans('addon.modal.settings.menu.categories'), 'Stores': this.trans('addon.modal.settings.menu.stores')};
  config: Object = {};
  renderComponents: boolean = false;

  getStorageAddonConfig(): void {
    Message.send((messageRequest: MessageRequest) => {
      messageRequest
          .setType(messageName.INITIALIZE_ADDON_CONFIG)
          .setResponseCallback((responseCallback: any) => {
            this.config = responseCallback['addonConfig'];
            this.renderComponents = true;
          });
    });
  }

  observeConfigStorage(): void {
    browser.storage.onChanged.addListener((storage) => {
      if(storage.addonConfig) {
        this.config = storage.addonConfig.newValue
      }
    })
  }

  created(): void {
    this.getStorageAddonConfig();
    this.observeConfigStorage();
  }

  beforeDestroy(): void {
    this.$emit('set-api-addon-config');
  }
}
