import browser from "../modules/chrome/nameSpace";
import Message from "../modules/chrome/message/MessageContent";
import PopupSection from "./sections/PopupSection";
import SerpDecoration from "./sections/SerpDecoration";
import MessageRequestResponse from "../modules/chrome/message/structures/MessageRequestResponse";
import Config from "../backend/Config";
import DomainRecognize from "./service/DomainRecognize";
import locale from "../backend/Locale";
import MessageRequest from "../modules/chrome/message/structures/MessageRequest";
import cashbackActivations from "../modules/cashbackActivations/index";
import ReviewPageHelper from "./service/ReviewPageHelper";
import ApiDataManager from "./service/ApiDataManager";
import isObject from "lodash/isObject";
import forEach from "lodash/forEach";
import Tab = browser.tabs.Tab;
import {lastCaaSuccessStorage, messageName} from "../modules/constants";

class RootContent {
  private dom: HTMLDocument;
  private tab: Tab;
  private locale: string;
  private configuration: Object;
  private domainRecognize: DomainRecognize;
  private config: Config;
  private trans: { [key: string]: string; };


  public constructor() {
    this.initializeBackgroundData()
      .then((messageRequest: MessageRequestResponse) => {
        this.init(messageRequest).catch((e) => console.debug(e));
    });
  }

  private async init(messageRequest: MessageRequestResponse){
    this.dom = document;
    this.locale = await locale.getLocale();
    this.trans = await this.getApiTranslations();
    this.tab = messageRequest.getTab();
    this.domainRecognize = await this.createDomainRecognize()
    this.config = new Config();
    cashbackActivations.saveFromRedirect(this.tab.url, this.locale);


    addEventListener('unhandledrejection', async (event) => {
      console.log('Error CONTENT promise: ', event)
    });

    addEventListener('error', (event) => {
      console.log('Error CONTET error: ', event);
    });

    this.renderSectionContent();
    this.senMessageToBackground();
  }

  private async createDomainRecognize() {
    const domainRecognize = new DomainRecognize(this.tab.url, this.locale);
    await domainRecognize.initRecognize();
    return domainRecognize;
  }

  private getApiTranslations(): Promise<{[index: string]:string}> {
    const translations = new ApiDataManager('api.translations', {locale: this.locale})
    return translations.getData();
  }

  private senMessageToBackground() {
    Message.send((messageRequest: MessageRequest) => {
      messageRequest
        .setType(messageName.CONTENT_IS_READY)
        .setValue('removeListener', true)
        .setValue('tab', this.tab)
        .setResponseCallback((responseCallback: lastCaaSuccessStorage) => {
          if (responseCallback && isObject(responseCallback)) {
            new ReviewPageHelper(responseCallback, this.locale, this.trans)
          }
        });
    });

    Message.send((messageRequest: MessageRequest) => {
      messageRequest
        .setType(messageName.SET_ADDON_INSTALLATION_ID_COOKIE)
    });
  }

  public getDom(): HTMLDocument {
    return this.dom;
  }

  public getTrans(): Object {
    return this.trans;
  }

  public getTab(): Tab {
    return this.tab;
  }

  public getLocale(): string {
    return this.locale;
  }

  public setConfiguration(value: Object) {
    this.configuration = value;
  }

  public getConfiguration(){
    return this.configuration;
  }

  public getDomainRecognize() {
    return this.domainRecognize;
  }

  public getConfig() {
    return this.config;
  }

  private initializeBackgroundData(): Promise<MessageRequestResponse> {
    return new Promise(resolve => {
      Message.addListener(messageName.INITIALIZE_POPUP, resolve);
    });
  }

  private renderSectionContent(): void {
    let section = null;
    const sections = [PopupSection, SerpDecoration];
    forEach(sections, (Section: any) => {
      if (Section.isEnabled(this.tab.url)) {
        section = new Section(this);
        section.render();
      }
    });
  }
}

export default RootContent;
