




























































import Component from 'vue-class-component'
import {Prop} from "vue-property-decorator";
import { mapGetters } from "vuex";

import AbstractOfferSection from "./../AbstractOfferSection.vue";

import Offer from "../../sections/structures/Offer";
import GaHelper from "../../service/GaHelper";
import Message from "../../../modules/chrome/message/MessageContent";
import MessageRequest from "../../../modules/chrome/message/structures/MessageRequest";
import {gaParameters, redirectType} from "../../../modules/constants";
import Shop from "../../sections/structures/Shop";
import ErrorTracker from "../../../modules/ErrorTracker";

@Component({
  computed: {
    ...mapGetters({
      getOffers: 'offers/getOffers'
    })
  },
})
export default class Offers extends AbstractOfferSection {
  @Prop(Boolean) isLogged!: boolean;
  @Prop(String) searchShopName!: string;
  @Prop(Boolean) isSubsidExist!: boolean;

  locale: string = this.$store.getters[('locale/getLocale')];
  offers: Array<Offer> = this.$store.getters[('offers/getOffers')];
  shop: Shop = this.$store.getters[('shop/getShop')];
  isClickedCodes: Array = [];
  isCopiedCodes: Array = [];

  isCashbackRedirect(shopCashbackEnabled: boolean): boolean {
    return this.isSubsidExist && shopCashbackEnabled;
  }

  async redirectToTab(active: boolean, offer: Offer, url: string): Promise<void> {
    try {
      this.sendGaEventRedirect();
      this.createTab(url, active);
    } catch (e) {
      ErrorTracker.trackError(e);
    }
  }

  async redirectPromoOffer(active: boolean, offer: Offer) {
    const isCashbackRedirect = this.isCashbackRedirect(offer.shopCashbackEnabled);
    const url: string = await this.generateOfferMetricUrl({
      offerId: offer.id,
      shopUrlName: offer.shopUrlName,
      locale: this.locale,
      versionRedirect: offer.versionRedirect,
      redirectType: redirectType.SHOP_COUPON_PROMO,
      isCashbackOfferRedirect: isCashbackRedirect
    });
    this.redirectToTab(active, offer, url);
  }

  redirectPseudoCodeOffer(active: boolean, offer: Offer) {
    const url: string = this.generatePicodiUrl(offer.id, this.locale, offer.shopUrlName);
    this.redirectToTab(active, offer, url);
  }

  clickCodeOffer(active: boolean, offer: Offer){
    this.copyTextToClipboard(offer.code)
      .then(() => {
        this.redirectCodeOffer(active, offer);
      })
      .catch((e) => {
        ErrorTracker.trackError(`Problem with copying - ${e}`)
      })
  }

  async redirectCodeOffer(active: boolean, offer: Offer) {
    const isCashbackRedirect = this.isCashbackRedirect(offer.shopCashbackEnabled);
    const url: string = await this.generateOfferMetricUrl({
      offerId: offer.id,
      shopUrlName: offer.shopUrlName,
      locale: this.locale,
      versionRedirect: offer.versionRedirect,
      redirectType: redirectType.SHOP_COUPON_CODE,
      isCashbackOfferRedirect: isCashbackRedirect
    });
    if(this.isClickedCodes.includes(offer.id)){
      return false;
    }
    this.isClickedCodes = this.isClickedCodes ? [...this.isClickedCodes, offer.id] : [offer.id];
    this.redirectToTab(active, offer, url);
  }

  copyCodeToClipboard(offer: Offer, sendGa: boolean = false) {
    this.copyTextToClipboard(offer.code)
      .then(() => {
        this.isCopiedCodes = this.isCopiedCodes ? [...this.isCopiedCodes, offer.id] : [offer.id];
        if(sendGa) {
          this.sendGaEventCopyCode();
        }
      })
  }

  copyTextToClipboard(copyText: string) {
    if(!copyText){
      return new Promise(reject => reject());
    }
    return navigator.clipboard.writeText(copyText);
  }

  sendGaEventRedirect(): void{
    GaHelper.pushEvent('click', {
      [gaParameters.PAGE_TITLE]: 'shop_coupons',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/offers/${this.shop.shopUrlName}/coupons`,
      [gaParameters.SHOP_URL_NAME]: this.shop.shopUrlName
    });
  }

  sendGaEventCopyCode(): void{
    GaHelper.pushEvent('copy-code', {
      [gaParameters.PAGE_TITLE]: 'shop_coupons',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/offers/${this.shop.shopUrlName}/coupons`,
      [gaParameters.SHOP_URL_NAME]: this.shop.shopUrlName
    });
  }

  mounted(): void{
    GaHelper.pushPageView({
      [gaParameters.PAGE_TITLE]: 'shop_coupons',
      [gaParameters.PAGE_LOCATION]: `/${this.locale}/offers/${this.shop.shopUrlName}/coupons`,
      [gaParameters.SHOP_URL_NAME]: this.shop.shopUrlName
    });
  }
}
